.viewpointDisplayRule {
  /*定制需求*/
}
.viewpointDisplayRule .form-base,
.viewpointDisplayRule .form-title:first-child,
.viewpointDisplayRule .form-group:not(:first-child) {
  overflow: hidden;
}
.viewpointDisplayRule .form-group .form-radio {
  vertical-align: middle;
}
.viewpointDisplayRule .form-group .radio-title {
  margin-right: 50px;
}
.viewpointDisplayRule .form-title .form-header em {
  color: #f65177;
  font-weight: normal;
}
.viewpointDisplayRule .form-group .input-item .label-normal {
  width: 100px;
}
.viewpointDisplayRule .form-group .input-item .input-normal {
  position: relative;
  vertical-align: middle;
}
.viewpointDisplayRule .form-group .input-item .input-normal > span {
  position: absolute;
  top: 7px;
  right: 5px;
  color: #666;
}
.viewpointDisplayRule .form-group .input-item .input-normal > span i.icon-error {
  color: #999;
}
.viewpointDisplayRule .form-group .input-item .label-normal.label-special {
  width: 190px;
}
.viewpointDisplayRule .cloud-ui-table .phone {
  width: 150px;
}
.viewpointDisplayRule .cloud-ui-table tr td {
  max-width: 150px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.viewpointDisplayRule .selected-picture {
  display: inline-block;
  height: 96px;
}
.viewpointDisplayRule .choose-picture {
  width: 100px;
  line-height: 35px;
  font-weight: normal;
  text-align: right;
  vertical-align: top;
  color: #666;
}
.viewpointDisplayRule .selected-picture i {
  position: absolute;
  top: 22px;
  left: 56px;
  padding-left: 14px;
  padding-top: 8px;
  width: 58px;
  height: 58px;
  background: #fff;
  border-radius: 50%;
  font-size: 28px;
}
.viewpointDisplayRule .selected-picture i:before {
  font-size: 40px;
  top: -6px;
  left: -6px;
  position: relative;
}
.viewpointDisplayRule .choose-background {
  position: relative;
  display: inline-block;
  margin-right: 15px;
  width: 170px;
  height: 100px;
  border-radius: 8px;
  background-color: #d8d8d8;
  color: #d8d8d8;
}
.viewpointDisplayRule .choose-background img {
  position: absolute;
  top: 0;
  width: 170px;
  height: 100px;
  border-radius: 8px;
}
.viewpointDisplayRule .choose-file {
  width: 100%;
  height: 100%;
  opacity: 0;
}



