.tree-view {
  margin-bottom: 20px;
  padding: 19px;
  background-color: white;
  overflow-y: auto;
  overflow-x: auto;
}
.tree-view li {
  position: relative;
  list-style-type: none;
  white-space: nowrap;
}
.tree-view li li {
  position: relative;
  margin: -10px 0 0 0;
  padding: 0 0 0 22px;
  list-style-type: none;
}
.tree-view li li li {
  position: relative;
  margin: -10px 0 0 0;
  padding: 0 0 0 22px;
  list-style-type: none;
}
.tree-view li li li li {
  position: relative;
  margin: -10px 0 0 0;
  padding: 0 0 0 22px;
  list-style-type: none;
}
.tree-view ul {
  margin-top: 0;
}
.tree-view ul ul {
  margin-top: 0;
}
.tree-view ul ul ul {
  margin-top: 0;
}
.tree-view ul ul ul ul {
  margin-top: 0;
}
.tree-view span {
  border-radius: 5px;
  display: inline-block;
  border: 0 solid #999999;
  text-decoration: none;
  cursor: pointer;
}
.tree-view ul span {
  border-radius: 5px;
  border: 0 solid #999999;
  text-decoration: none;
}
.tree-view li li li li span {
  border-radius: 5px;
  border: 0 solid #999999;
  text-decoration: none;
}
.tree-view span span {
  cursor: pointer;
}
.tree-view li:last-child::before {
  height: 50px;
}
.tree-view li ul.line {
  background: url(../../../assets/images/shu.png) 0 0 repeat-y;
}
.tree-view .icon-correct {
  color: #50c14e !important;
}
.tree-view .icon-error {
  color: #f65177 !important;
}
.tree-view .icon-query {
  color: #9fa4b4 !important;
}
.tree-view .red {
  color: #f65177;
}



