.store-selected-modal .form-base,
.store-selected-modal .form-group {
  margin-bottom: 30px;
}
.store-selected-modal .form-group .input-item label {
  width: auto;
  text-align: left;
}
.store-selected-modal .cloud-ui-table {
  margin: 0;
  max-width: 1000px;
}
.store-selected-modal .cloud-ui-table thead tr th {
  text-align: center;
}
.store-selected-modal .cloud-ui-table tbody tr td {
  text-align: center;
  border-right: 1px solid #ddd;
}
.store-selected-modal .cloud-ui-pagination {
  margin-top: 0;
}
.store-selected-modal .cloud-ui-pagination .pagination {
  margin-bottom: 0;
}



