.QA-check .form-group {
  margin-bottom: 0;
}
.QA-check .session-title {
  display: inline-block;
  margin: 10px 20px 20px 0;
  font-size: 16px;
  color: #151E25;
  cursor: pointer;
}
.QA-check .session-title.active {
  color: #5093E1;
}
.QA-check .form-title {
  overflow: hidden;
  padding-bottom: 0;
}
.QA-check .form-title .search-item {
  margin-right: 20px;
  float: right;
  width: 250px;
}
.QA-check .form-title .search-item .input-normal {
  width: 250px;
}
.QA-check .form-title .form-checkbox {
  margin-top: 10px;
  margin-right: 5px;
  float: right;
}
.QA-check .form-title .tip {
  margin-top: 10px;
  float: right;
}
.QA-check .list {
  min-height: 400px;
  overflow: auto;
}
.QA-check .QA {
  position: relative;
  margin-bottom: 20px;
  padding: 15px;
  background: #F2F2F2;
  border-radius: 8px;
}
.QA-check .QA span {
  margin-right: 10px;
  display: inline-block;
  font-size: 12px;
  color: #999999;
  line-height: 17px;
  vertical-align: middle;
}
.QA-check .QA .content div {
  margin-bottom: 20px;
}
.QA-check .QA .user {
  text-align: left;
}
.QA-check .QA .user span:first-of-type {
  padding: 0 5px;
  width: 24px;
  height: 24px;
  background: #4AD2BA;
  border-radius: 100%;
  font-size: 14px;
  color: #FFFFFF;
  line-height: 24px;
}
.QA-check .QA .answer {
  overflow: hidden;
}
.QA-check .QA .answer .user span:first-of-type {
  background: #5093E1;
}
.QA-check .QA .answer a {
  float: left;
  margin-top: 10px;
  margin-left: 38px;
  cursor: pointer;
  font-size: 14px;
  color: #5093E1;
}
.QA-check .QA .text {
  margin-left: 38px;
  font-size: 14px;
  line-height: 24px;
  color: #333333;
  text-align: left;
  overflow: hidden;
  max-width: 600px;
}
.QA-check .QA .answer .text {
  max-width: 600px;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}
.QA-check .QA .operate {
  font-size: 14px;
  color: #666666;
  line-height: 32px;
  border-top: 1px solid #E4E4E4;
}
.QA-check .QA .operate .checked {
  padding: 10px 10px 0 10px;
}
.QA-check .QA .operate .checked p {
  text-align: left;
  font-size: 14px;
  color: #666666;
  line-height: 32px;
}
.QA-check .QA .operate .checked span {
  margin-right: 40px;
  font-size: 14px;
  color: #666666;
  line-height: 32px;
}
.QA-check .QA .operate .uncheck {
  padding-top: 10px;
  overflow: hidden;
}
.QA-check .QA .operate .uncheck button {
  margin-right: 10px;
  float: right;
}
.QA-check .QA .price {
  position: absolute;
  top: 25px;
  right: 100px;
  line-height: 25px;
  font-size: 18px;
  font-weight: 400;
  color: #FD0000;
}
.QA-check .QA .result {
  position: absolute;
  right: -28px;
  top: 17px;
  width: 120px;
  height: 30px;
  font-size: 14px;
  line-height: 30px;
  font-weight: bold;
  transform: rotateZ(45deg);
  text-align: center;
  color: #FFFFFF;
}
.QA-check .QA .result::before {
  content: '';
  position: absolute;
  left: -1px;
  top: -1px;
  width: 0;
  height: 0;
  border-top: 16px solid white;
  border-bottom: 15px solid transparent;
  border-left: 15px solid white;
  border-right: 15px solid transparent;
}
.QA-check .QA .result::after {
  content: '';
  position: absolute;
  right: -1px;
  top: -1px;
  width: 0;
  height: 0;
  border-top: 16px solid white;
  border-bottom: 15px solid transparent;
  border-left: 15px solid transparent;
  border-right: 15px solid white;
}
.QA-check .QA .result.red {
  background: #FF3333;
}
.QA-check .QA .result.orange {
  background: #FF9900;
}
.QA-check .QA .result.blue {
  background: #5093E1;
}
.QA-check .bottom {
  margin-top: 30px;
}
.QA-check .bottom button {
  margin: 0 auto;
  padding: 5px 10px;
  display: block;
  width: 100px;
  font-weight: bold;
  border: 2px solid #5093E1;
  color: #5093E1;
  border-radius: 4px;
}



