.liveManage .setting {
  padding: 10px;
  color: #666;
}
.liveManage .setting .input-item {
  display: inline-block;
}
.liveManage .setting .form-control {
  width: 190px;
}
.liveManage .setting .btn-normal {
  margin-left: 40px;
}
.liveManage .account-box {
  background: #fff;
  padding: 10px;
}
.liveManage .account-box .f-title {
  clear: both;
  width: 98%;
  border-width: 1px 1px 0 1px;
  border-radius: 4px 4px 0 0;
  border-style: solid;
  border-color: #ccc;
}
.liveManage .account-box .f-title .f-title-item {
  padding-top: 15px;
  width: 20px;
  height: 40px;
  line-height: 40px;
  cursor: pointer;
}
.liveManage .account-box .f-title .f-title-item i {
  display: block;
  margin: 0 auto;
}
.liveManage .account-box .f-title .f-title-left {
  float: left;
  margin-left: 5px;
  padding-top: 15px;
  width: 20px;
  height: 40px;
  line-height: 40px;
  cursor: pointer;
}
.liveManage .account-box .f-title .f-title-left i {
  display: block;
  margin: 0 auto;
}
.liveManage .account-box .f-title .f-title-right {
  float: right;
  margin-right: 5px;
  padding-top: 15px;
  width: 20px;
  height: 40px;
  line-height: 40px;
  cursor: pointer;
}
.liveManage .account-box .f-title .f-title-right i {
  display: block;
  margin: 0 auto;
}
.liveManage .account-box .f-title .f-title-center {
  margin: 0 auto;
  width: 100px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  font-size: 14px;
  color: #151E25;
}
.liveManage .f-rili-table {
  border-left: 1px solid #ccc;
}
.liveManage .f-rili-table .f-tbody {
  clear: both;
  overflow: hidden;
  border-width: 0 0 0 1px;
  border-color: #ccc;
}
.liveManage .f-rili-table .f-tbody:before {
  content: '';
  position: absolute;
  height: 100%;
  /*border-left: 1px solid #ccc;*/
}
.liveManage .f-rili-table .f-rili-th {
  float: left;
  width: 14%;
  text-align: center;
  line-height: 46px;
  height: 46px;
  border-width: 1px 1px 1px 0;
  border-style: solid;
  border-color: #ccc;
  font-size: 14px;
  color: #151E25;
}
.liveManage .f-rili-table .f-rili-th:first-child {
  /*border-width: 1px;*/
  border-style: solid;
  border-color: #ccc;
}
.liveManage .f-rili-table .f-td {
  border-width: 0 1px 1px 0;
  border-style: solid;
  border-color: #ccc;
  float: left;
  width: 14%;
  height: 108px;
  text-align: center;
  box-sizing: border-box;
  overflow: auto;
}
.liveManage .f-rili-table .f-day {
  margin: 5px;
  float: right;
  width: 18px;
  height: 18px;
  line-height: 18px;
  font-size: 12px;
  color: white;
  background-color: #5093E1;
  border-radius: 100%;
}
.liveManage .f-rili-table .f-null {
  background-color: #E2E4EA;
}
.liveManage .f-rili-table .f-null-date {
  float: right;
  line-height: 10px;
  width: 10px;
  margin: 10px;
}
.liveManage .f-rili-table .course-base {
  line-height: 24px;
  width: 90%;
  height: 24px;
  border: none;
  border-radius: 1px;
  background-color: #333333;
  color: white;
  cursor: pointer;
}
.liveManage .f-rili-table .course-btn {
  line-height: 24px;
}
.liveManage .f-rili-table .course-empty {
  line-height: 24px;
  width: 90%;
  height: 24px;
  border: none;
  border-radius: 1px;
  color: #666;
  cursor: pointer;
  background-color: #E2E4EA;
}
.liveManage .f-today {
  background-color: #FAF1DD;
}



