@import "~variable";
@import "~mixins";

.sensitiveWords {
  .form-group {
    overflow: hidden;
  }
  .form-group .pull-right {
    margin-left: 10px;
  }
  .cloud-ui-table th.little {
    width: 50px;
  }
  .cloud-ui-table .left {
    width: 200px;
    text-align: left;
  }
  .cloud-ui-table .remark {
    display: inline-block;
    max-width: 250px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .cloud-ui-table td a {
    outline: none;
  }
  .form-group .dropdown-menu li {
    width: 188px;
  }
  .import-excel {
    display: none;
  }
  .select-btn {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    display: inline-block;
    width: 151px;
  }
  .select-list {
    padding-right: 15px;
    width: 188px;
    overflow: hidden;
    display: inline-block;
    text-overflow: ellipsis;
  }
  .select-dropdown {
    max-height: 300px;
    overflow: auto;
  }
  .no-data {
    padding: 5px 0;
    text-align: center;
    font-size: 16px;
  }
}



