.recommendCombine .form-base,
.recommendCombine .form-title:first-child,
.recommendCombine .form-group:not(:first-child) {
  overflow: hidden;
}
.recommendCombine .form-group .form-radio {
  vertical-align: middle;
}
.recommendCombine .form-group .radio-title {
  margin-right: 50px;
}
.recommendCombine .form-title .form-header em {
  color: #f65177;
  font-weight: normal;
}
.recommendCombine .form-group .input-item {
  min-width: 0;
}
.recommendCombine .form-group .input-item .input-normal {
  position: relative;
  vertical-align: middle;
}
.recommendCombine .form-group .input-item .input-normal > span {
  position: absolute;
  top: 7px;
  right: 5px;
  color: #666;
}
.recommendCombine .form-group .input-item .input-normal > span i.icon-error {
  color: #999;
}
.recommendCombine .form-group .input-item .label-normal.label-special {
  width: 190px;
}
.recommendCombine .form-control[readonly] {
  background-color: #fff;
}
.recommendCombine .link-input {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  padding-right: 20px;
}



