  color: #50c14e;
}
.edit-info-modal .remarks .label-normal {
  vertical-align: top;
}
.edit-info-modal .remarks .input-normal {
  position: relative;
  top: 10px;
}
.edit-info-modal .remarks .input-normal .form-control {
  height: 100px;
}



