@import "~variable";
@import "~mixins";

.customerManage {
  .search-item {
    float: right;
    margin-right: 20px;
  }
  .table-content {
    height: 460px;
    margin-top: 0;
    .cloud-ui-pagination {
      position: absolute;
      bottom: 0;
      right: 4px;
    }
    .detail-content {
      padding-top: 10px;
      padding-bottom: 20px;
    }
    label {
      font-weight: normal;
    }
    .input-item.ownership {
      padding-left: 53px;
    }
    .input-item.phone {
      padding-left: 20px;
    }
  }
  .content-list {
    .disable-msg {
      position: absolute;
      right: 0;
    }
    .list-forty {
      padding-left: 30px;
    }
  }
  .cloud-ui-pagination{
    .pagination {
      margin: 5px;
    }
  }
  .btn-normal {
    float: right;
  }
  .form-title {
       padding-bottom: 14px;
     }
  .icon-jinyan {
    color: @btnRed;
  }
  .btn-green {
    padding: 0;
    a {
      display: inline-block;
      width: 100%;
      height: 100%;
      color: @fontWhite;
    }
  }
  .client-list {
    .cloud-ui-table tbody tr td {
      border-top: none;
    }
    .cloud-ui-table tr:hover {
      background-color: @fontWhite;
    }
    .table {
      color: @fontBase;
    }
    .table>thead>tr>th {
      color: @clientTable;
      border-bottom: none;
    }
    .list-thirty {
      float: left;
    }
  }
}
.client {
  &-avatar {
    width: 80px;
    height: 80px;
    margin: 0 auto;
    border-radius: 50%;
    background-color: @bgAddPic;
  }
  &-intro {
    height: 26px;
    line-height: 26px;
    margin-top: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &-name {
    padding-right: 8px;
  }
  &-position {
    color: @clientFont;
    padding-right: 5px;
  }

  &-symbol {
    width: 32px;
    height: 20px;
    line-height: 20px;
    text-align: center;
    background-color: @clientGreen;
    border-radius: 7px;
    color: @fontWhite;
    font-size: 12px;
  }
  &-symbol-limit {
    width: 32px;
    height: 20px;
    line-height: 20px;
    text-align: center;
    background-color: @btnRed;
    border-radius: 7px;
    color: @fontWhite;
    font-size: 12px;
  }
  &-info {
    //width: 80%;
    margin: 0 auto;
    padding: 10px 0 0 0;
    font-size: 12px;
    label {
      float: left;
      width: 40%;
      min-width: 76px;
      text-align: right;
      font-weight: normal;
      color: @fontBase;
    }
    span {
      width: 60%;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      float: left;
      color: @fontBase;
    }
  }
  &-list {
    display: inline-block;
    position: relative;
    width: 100%;
    padding: 14px 20px 0 20px;
    h4 {
      display: inline-block;
      height: 26px;
      width: 100%;
      border-bottom: solid 1px @clientBorder;
      color: @headerNavFocus;
      font-size: 14px;
      padding: 0 5px;
      margin-bottom: 10px;
    }
    .detail-more {
      position: absolute;
      width: 12%;
      float: right;
      font-size: 12px;
      color: @clientTable;
      cursor: pointer;
      right: 0;
      top: 14px;
    }
    i {
      padding-bottom: 4px;
    }
    label {
      float: left;
      width: 50%;
      min-width: 70px;
      text-align: right;
      font-weight: normal;
    }
    span {
      width: 50%;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      float: left;
    }
    .cloud-ui-table {
      width: 95%;
      margin: 0 auto;
      border: none;
      font-size: 12px;
    }
    th {
      background-color: @fontWhite;
      color: @clientTable;
      font-weight: 100;
    }
    .no-data {
      padding: 5px 0;
      text-align: center;
      font-size: 12px;
    }
  }
  &-content {
    margin: 0 auto;
    //width: 80%;
    height: 25px;
    line-height: 25px;
    font-size: 12px;
    color: @fontBase;
  }
  &-search {
    width: 100px;
    height: 62px;
    margin: 0 auto;
    padding: 15px 0;
  }
}



