.storeManage .first-line .form-group {
  display: inline-block;
  width: 100%;
  margin-bottom: 6px;
}
.storeManage .form-group .input-item {
  min-width: 270px;
  padding-top: 6px;
}
.storeManage .form-group .input-item .label-normal {
  width: 75px;
}
.storeManage .search {
  padding-top: 6px;
}
.storeManage .add-more .input-normal {
  display: inline-block;
}
.storeManage .cloud-ui-table .store-tag .tag-content {
  max-width: 200px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  text-align: center;
}
.storeManage .cloud-ui-table tr {
  cursor: pointer;
}
.storeManage .cloud-ui-table tr:hover {
  background-color: rgba(80, 147, 225, 0.3);
}
.storeManage .cloud-ui-table .blue {
  background-color: rgba(80, 147, 225, 0.3);
}
.storeManage .no-data {
  text-align: center;
  margin-top: 20px;
}
.storeManage .icon {
  margin-right: 20px;
}



