.add-bg div {
  margin: 0 auto;
}
.add-bg .choose-picture {
  position: relative;
  width: 497px;
  height: 153px;
}
.add-bg .choose-picture span {
  position: relative;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.add-bg .choose-picture i {
  font-size: 80px;
  color: #fff;
  border-radius: 50%;
}
.add-bg .choose-picture input {
  position: absolute;
  width: 136px;
  height: 70px;
  opacity: 0;
}
.add-bg .choose-picture img {
  position: absolute;
  width: 100%;
  height: 100%;
}
.add-bg .pic-gray {
  background-color: #ccc;
}



