@import "~variable";
@import "~mixins";

.add-product-modal {
  .form-group .input-item label {
    width: auto;
    text-align: left;
  }
  .cloud-ui-table {
    margin: 0;
    max-width: 1000px;
    border: none;
  }
  .cloud-ui-table thead tr th {
    text-align: center;
    color: #A6A6A6;
    border-bottom: none !important;
    background-color: transparent;
  }
  .cloud-ui-table tbody tr td {
    text-align: center;
  }
  .cloud-ui-table .red {
    color: @btnRed;
  }
  .cloud-ui-table .green {
    color: @btnGreen;
  }
  .cloud-ui-pagination {
    margin-top: 0;
    .pagination {
      margin-bottom: 0;
    }
  }
}


