@import "~variable";
@import "~mixins";

.liveMonitorList {
  .search-item {
    float: right;
    // width: 150px;
  }
  .date-box .date-label-normal {
    margin-right: 5px;
    width: 120px;
    text-align: right;
  }
  .date-box .date-input-normal {
    width: 150px;
  }
  .date-box .date-label-normal {
    width: 90px;
  }
  .input-item.status .label-normal {
    width: 90px;
  }
  .input-item.status .input-normal {
    width: 150px;
  }
  .main-list .operate {
    color: #3e7cdd;
  }
  .main-list tr:hover {
    .operate {
      cursor: pointer;
      color: white;
    }
  }
  .no-data {
    padding: 10px;
    text-align: center;
  }
}



