.recommendInvest .form-base,
.recommendInvest .form-title:first-child,
.recommendInvest .form-group:not(:first-child) {
  overflow: hidden;
}
.recommendInvest .form-group .form-radio {
  vertical-align: middle;
}
.recommendInvest .form-group .radio-title {
  margin-right: 50px;
}
.recommendInvest .form-title .form-header em {
  color: #f65177;
  font-weight: normal;
}
.recommendInvest .form-group .input-item {
  min-width: 0;
}
.recommendInvest .form-group .input-item .input-normal {
  position: relative;
  vertical-align: middle;
  width: 170px;
}
.recommendInvest .form-group .input-item .input-normal > span {
  position: absolute;
  top: 7px;
  right: 5px;
  color: #666;
}
.recommendInvest .form-group .input-item .label-normal {
  width: auto;
}
.recommendInvest .form-group .input-item .label-normal.label-special {
  width: 190px;
}
.recommendInvest .cloud-ui-table tr td {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}



