

@import "~variable";
@import "~mixins";

.form-base {
  margin-bottom: 20px;
  padding: 20px;
  border: 1px solid @borderColor;
  background-color: @bgForm;
}
.form-title {
//  margin: 10px 0;
  padding-bottom: 24px;
}
 .form-title .form-header {
  display: inline-block;
}
 .form-title .header-line {
  margin-right: 10px;
  margin-left: 1px;
  width: 3px;
  height: 20px;
  vertical-align: middle;
  background-color: @bgLine;
}
 .form-title .header-title {
  vertical-align: middle;
  color: @bgTitleColor;
}
 .form-horizontal .form-group {
  margin: 0 0 10px 0;
}

//面包屑导航
.breadcrumb {
  padding-left: 0;
  background-color: transparent;
}
.breadcrumb > li + li:before {
  display: none;
}
.breadcrumb > li > span {
  padding-right: 10px;
}


//单选框
.form-radio {
  left: 1px;
}

//输入框
.form-group .input-item {
  display: inline-block;
  min-width: 310px;
}
.form-group .input-item .control-label {
  height: 35px;
  line-height: 35px;
  margin-bottom: 0;
  padding-right: 0;
  text-align: right;
  color: @fontBase;
  font-weight: normal;
}
.form-group .input-item .label-normal {
  width: 120px;
}
.form-group .input-item .label-big {
  width: 150px;
}
.form-group .search-item .form-control {
  height: 36px;
  box-shadow: none;
}
.form-group .input-item .form-control {
  height: 35px;
  box-shadow: none;
}
.form-group .input-item .input-normal {
  display: inline-block;
  width: 190px;
}
.form-group .input-item .input-big {
  display: inline-block;
  width: 300px;
}
.form-group .input-item .control-label em {
  font-size: 20px;
  font-weight: normal;
  vertical-align: middle;
  color: @btnRed;
}
.form-group .input-item .message-error {
  clear: both;
  display: none;
  color: @errorColor;
}
.form-group .input-item .input-error {
  .form-control {
    border-color: @errorColor;
  }
  .message-error {
    display: block;
  }
}
.form-group .input-item .input-normal {
  vertical-align: middle;
  .text-area {
    width: 500px;
    height: 80px;
  }
}

//搜索框
.form-group .search-item {
  display: inline-block;
  width: 190px;
}
.form-group .input-group .input-group-btn {
  width: 35px;
  height: 35px;
  text-align: center;
  border-radius: 0 4px 4px 0;
  border: 1px solid @borderInputColor;
  border-left: none;
  font-size: 18px;
  background-color: #eaeaeb;
  color: @fontBase;
}
.form-group .caret {
  float: right;
  position: relative;
  top: 9px;
}

//日期选择
.date-box .date-label-normal {
  padding-right: 0;
  padding-top: 7px;
  font-weight: normal;
  color: @fontBase;
}
.date-box .date-input-normal {
  padding: 0;
  width: 190px;
}
.date-box .input-box .form-control {
  box-shadow: none;
}
.date-box .input-box .date-select {
  position: absolute;
  right: 10px;
  height: 35px;
  line-height: 30px;
  font-size: 20px;
  color: @fontBase;
}
.date-box .date-input-normal .dropdown-menu {
  box-shadow: none;
}
.date-height {
  height: 35px;
}

//下拉列表
.form-group .icon-pull-down {
  line-height: 20px;
}
.form-group .input-box .dropdown-menu {
  left: 15px;
}
.form-group .input-box .form-control .caret {
  float: right;
  position: relative;
  top: 9px;
}
.form-group .form-control .pd-content {
  float: left;
}
.form-group .form-control .pd-arrow {
  float: right;
  margin-right: -10px;
  position: relative;
  top: 3px;
}
.form-group ul.dropdown-menu {
  min-width: 190px;
  max-height: 500px;
  overflow: scroll;
}

//图片上传
.form-group .upload-box {
  position: relative;
  display: inline-block;
  padding: 50px 100px;
  background-color: @bgAddPic;
}
.form-group .upload-box .upload-label {
  margin-bottom: 0;
}
.form-group .upload-box .upload-input {
  position: absolute;
  top: 30px;
  left: 80px;
  width: 80px;
  height: 80px;
  opacity: 0;
  cursor: pointer;
}
.form-group .upload-box .upload-label .icon-add-picture {
  font-size: 40px;
}
.centre {
  &-head,
  &-content {
//    display: inline-block;
    width: 100%;
    border: solid 1px @borderColor;
    background-color: @fontWhite;
  }
  &-head {
    height: 155px;
    margin-bottom: 10px;
    padding-right: 15px;
  }
  &-content {
    padding: 15px;
  }
}
.show-more {
  display: inline-block;
  height: 32px;
  line-height: 32px;
  padding-left: 17px;
  font-size: 14px;
  color: @headerNavFocus;
  cursor: pointer;
}

.text-middle {
  text-align: center;
}
.error {
  color: @errorColor;
}

//tab切换
.tab-change {
  border-bottom: 3px solid @headerNavFocus;
  button {
    width: 120px;
    height: 36px;
    border-radius: 4px 4px 0 0;
    color: @fontBase;
  }
  .tab-active {
    background-color: @headerNavFocus;
    color: @fontWhite;
  }
}
.excel-export {
  display: none;
}



