.package-detail-modal .form-group {
  overflow: hidden;
}
.package-detail-modal .form-group .input-item {
  min-width: 200px;
}
.package-detail-modal .form-group .form-control {
  display: inline-block;
}
.package-detail-modal .form-group .combine-right {
  width: 100px;
}
.package-detail-modal .form-group .btn-search {
  float: right;
  display: inline-block;
  padding: 5px 40px;
  border-radius: 4px;
  background-color: #5093e1;
  line-height: 25px;
  font-size: 16px;
  color: #fff;
}
.package-detail-modal .table-responsive {
  max-width: 700px;
}
.package-detail-modal .cloud-ui-table {
  border-right: none;
}
.package-detail-modal .cloud-ui-table tbody td {
  border-right: 1px solid #ddd;
  line-height: 30px;
}
.package-detail-modal .cloud-ui-table thead th,
.package-detail-modal .cloud-ui-table tbody td {
  text-align: center;
}
.package-detail-modal .cloud-ui-table tbody .down {
  cursor: pointer;
}
.package-detail-modal .cloud-ui-table .operate {
  padding: 3px 5px;
  color: white;
  background-color: #3e7cdd;
  border-radius: 4px;
}
.package-detail-modal th.list-width {
  overflow: visible;
}
.package-detail-modal .selected-block {
  top: -7px;
}
.package-detail-modal .selected-block button {
  padding: 0;
  text-align: center;
  font-size: 14px;
  color: #070707;
  background-color: transparent;
  border: none;
  box-shadow: none;
}
.package-detail-modal .selected-block button i {
  font-size: 18px;
  margin-left: -10px;
  vertical-align: -2px;
}
.package-detail-modal .selected-block .select-dropdown {
  padding: 0;
  min-width: 125px;
  border-radius: 0;
}
.package-detail-modal .selected-block .select-dropdown:before {
  display: block;
  position: absolute;
  right: 55px;
  top: -11px;
  content: "";
  width: 0;
  height: 0;
  border-width: 5px;
  border-style: solid;
  border-color: transparent transparent #ddd transparent;
}
.package-detail-modal .selected-block .select-dropdown:after {
  display: block;
  position: absolute;
  right: 55px;
  top: -10px;
  content: "";
  width: 0;
  height: 0;
  border-width: 5px;
  border-style: solid;
  border-color: transparent transparent white transparent;
}
.package-detail-modal .selected-block .select-dropdown li {
  height: 35px;
  text-align: center;
}
.package-detail-modal .selected-block .select-dropdown li:not(:last-of-type) {
  border-bottom: 1px solid #ddd;
}
.package-detail-modal .selected-block .select-dropdown li a {
  line-height: 28px;
}
.package-detail-modal .selected-block .select-dropdown li a:hover {
  background-color: #5093e1;
  color: #fff;
}
.package-detail-modal .cloud-ui-table tbody td .btn-status {
  padding: 2px 15px;
  line-height: 20px;
  font-size: 14px;
  color: #fff;
  border-radius: 4px;
  background-color: #b2b7c2;
}
.package-detail-modal .cloud-ui-table tbody td button.online {
  background-color: #5093e1;
}
.package-detail-modal .no-data {
  text-align: center;
}
.package-detail-modal .btn-page {
  float: right;
  margin-bottom: 20px;
}
.package-detail-modal .btn-page button {
  padding: 0 10px;
  border: 1px solid #ddd;
  color: #666;
  border-radius: 3px;
  font-size: 12px;
  line-height: 22px;
}
.package-detail-modal .btn-page button:hover {
  background-color: #5093e1;
  color: #fff;
  border-color: #fff;
}
.package-detail-modal .btn-page .pre {
  margin-right: 10px;
}
.package-detail-modal .btn-page .disable,
.package-detail-modal .btn-page .disable:hover {
  background-color: #ddd;
  color: #666;
  border-color: #ddd;
  cursor: not-allowed;
}



