.page-template {
  background-color: #f4f5f9;
}
.page-template .template-header {
  margin: 0 0 20px 0;
  padding: 50px;
  text-align: center;
}
.page-template .template-header h1 {
  font-weight: bolder;
}
.page-template .template-body {
  padding: 0 30px;
  width: 100%;
  height: 100%;
}
.page-template .template-body h4 {
  margin-top: 50px;
  margin-bottom: 20px;
  font-weight: bolder;
}



