.fansSpeechMonitor .form-group .dropdown-menu li {
  width: 188px;
}
.fansSpeechMonitor .form-group {
  height: 35px;
}
.fansSpeechMonitor .form-group .input-item {
  min-width: 0;
}
.fansSpeechMonitor .form-group .pull-right {
  margin-left: 10px;
}
.fansSpeechMonitor .cloud-ui-table .box {
  width: 50px;
}
.fansSpeechMonitor .cloud-ui-table .remark {
  display: inline-block;
  max-width: 200px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.fansSpeechMonitor .cloud-ui-table .remark .content {
  display: inline-block;
}
.fansSpeechMonitor .cloud-ui-table td {
  vertical-align: middle;
}
.fansSpeechMonitor .cloud-ui-table .icon {
  margin-right: 10px;
  font-size: 14px;
  color: #f65177;
}
.fansSpeechMonitor .popover {
  max-width: none;
}
.fansSpeechMonitor .popover-text {
  margin-bottom: 5px;
  font-weight: 300;
}
.fansSpeechMonitor .popover-text .unit {
  display: inline-block;
  width: 100px;
  text-align: right;
  vertical-align: middle;
}
.fansSpeechMonitor .popover-text .content {
  display: inline-block;
  width: auto;
  vertical-align: middle;
}
.fansSpeechMonitor .popover-text .self-stock {
  background-color: orange;
  border-radius: 4px;
  color: #fff;
}
.fansSpeechMonitor .popover-text .error-text {
  background-color: #ce8483;
}
.fansSpeechMonitor .no-data {
  padding: 5px;
  text-align: center;
  font-size: 16px;
  color: #999;
}
.fansSpeechMonitor .no-data i {
  font-size: 80px;
  color: #999;
}



