@import "~variable";
@import "~mixins";

.order-modal {
  padding: 15px;
  width: 1000px;
  max-height: 460px;
  min-height: 150px;
  overflow: auto;
  .form-group {
    padding-top: 15px;
  }
  .modal-footer {
    margin-top: 20px;
  }
  .sub-list {
    max-height: 200px;
    overflow: auto;
    text-align: center;
  }
  th {
    text-align: center;
  }
  .form-title {
    padding-bottom: 0;
  }
}




