.log-detail .form-group {
  padding-top: 15px;
}
.log-detail .modal-footer {
  margin-top: 20px;
}
.log-detail .error {
  padding: 0;
  font-size: 14px;
}
.log-detail .error-tips {
  padding-left: 126px;
}
.log-detail .error-tips p {
  text-align: left;
}
.log-detail .textarea-normal {
  display: inline-block;
  width: 81%;
}
.log-detail .textarea-normal .form-control {
  float: left;
  margin-left: 4px;
  height: 46px;
}
.log-detail .col-sm-12 label {
  float: left;
}
.log-detail .date {
  position: relative;
}
.log-detail .date span {
  position: absolute;
  left: 10px;
  top: 0;
  height: 35px;
  line-height: 35px;
  font-size: 14px;
  color: #555;
}



