@import "~variable";
@import "~mixins";
@import (less) "~swiper/dist/css/swiper.css";

.imageConfig {
  .centre-head {
    height: 60px;
  }
  .form-radio {
    margin-left: 25px;
    label {
      top: 2px;
    }
  }
  .form-title {
    padding-top: 14px;
  }
  .image-search {
    display: inline-block;
    width: 100px;
    padding-top: 16px;
    float: right;
  }
  .radio-txt {
    color: @fontBase;
  }
  .input-state {
    font-size: 12px;
    color: @btnRed;
  }
  .centre-content {
    display: inline-block;
    .label-fix {
      width: 97px;
    }
    .form-title {
      padding: 0;
    }
  }
  .img-circle {
    display: inline-block;
    width: 100%;
    height: 210px;
    margin-top: 17px;
  }
  .image-carousel {
    display: inline-block;
    width: 400px;
    height: 210px;
    margin-top: 17px;
    .icon-primary {
      margin-right: 0;
    }
  }
  .annotation {
    display: inline-block;
    height: 20px;
    width: 100%;
    margin-bottom: 30px;
  }
  .pic-display {
    position: relative;
    padding: 10px;
    background-color: #ddd;
    img {
      width: 100%;
      height: 180px;
    }
    .pic-delete {
      position: absolute;
      right: 2px;
      top: 1px;
      margin-right: 9px;
      padding: 1px 3px 1px 3px;
      background-color: rgba(255, 255, 255, 0.7);
      color: rgba(0, 0, 0, 0.7);
      font-weight: bolder;
      line-height: 1;
      -webkit-font-smoothing: antialiased;
    }
  }
  .link-address {
    display: inline-block;
    position: relative;
    margin-top: 2px;
    width: 100%;
    height: 24px;
    p {
      padding-left: 10px;
      width: 325px;
      height: 24px;
      line-height: 24px;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
    }
    i {
      position: absolute;
      right: 0;
      top: 0;
      font-size: 20px;
    }
    a {
      cursor: pointer;
    }
    a:hover {
      text-decoration: underline;
    }
  }
  .swiper-container {
    height: 232px;
    img {
      height: 130px;
    }
    .swiper-slide {
      width: 33%;
      img {
        padding-right: 10px;
        width: 100%;
      }
    }
    .pic-delete {
      position: absolute;
      right: 2px;
      top: 1px;
      margin-right: 9px;
      padding: 1px 3px 1px 3px;
      background-color: rgba(255, 255, 255, 0.7);
      color: rgba(0, 0, 0, 0.7);
      font-weight: bolder;
      line-height: 1;
      -webkit-font-smoothing: antialiased;
    }
    .swiper-button-prev,
    .swiper-button-next {
      background: none;
    }
  }
  .loadhide {
    opacity: 0.5;
    cursor: not-allowed;
  }
}



