@import "~variable";
@import "~mixins";

.btn-normal {
  display: inline-block;
  border-radius: 4px;
  width: auto;
  height: 32px;
  text-align: center;
  line-height: 32px;
  color: @fontWhite;
  cursor: pointer;
  font-family: STHeitiSC-Light;
}
.btn-blue {
  background-color: @btnBlue;
  &:hover {
    background-color: @btnBlueHover;
  }
  &:active {
    background-color: @btnBlueActive;
  }
}
.btn-green {
  background-color: @btnGreen;
  &:hover {
    background-color: @btnGreenHover;
  }
  &:active {
    background-color: @btnGreenActive;
  }
}
.btn-red {
  background-color: @btnRed;
  &:hover {
    background-color: @btnRedHover;
  }
  &:active {
    background-color: @btnRedActive;
  }
}
.btn-right {
  margin-right: 10px;
}
.choose-all {
  position: relative;
  input {
    display: none;
  }
  label {
    position: absolute;
    top: 8px;
//    left: 20px;
    max-width: none;
    margin-bottom: 0;
    width: 18px;
    height: 18px;
    border-color: transparent;
    background-color: @fontWhite;
  }
}
.btn-disabled {
  background-color: @stateFont;
  cursor: not-allowed;
  border-radius: 4px;
  color: #fff;
}
.btn-disabled:hover {
  cursor: default;
  background-color: @stateFont;
}
.turn-around {
  -webkit-animation: circle 1.5s infinite linear;
}
@-webkit-keyframes circle{
  0%{ transform:rotate(0deg); }
  100%{ transform:rotate(360deg); }
}



