.QACheckDetailModal {
  margin-bottom: 20px;
  width: 600px;
  height: 400px;
  overflow: auto;
  .content p {
    padding: 0;
  }
  .content span {
    margin-right: 10px;
    display: inline-block;
    font-size: 12px;
    color: #999999;
    line-height: 17px;
    vertical-align: middle;
  }
  .content div {
    margin-bottom: 20px;
  }
  .user {
    text-align: left;
    span:first-of-type {
      padding: 0 5px;
      width: 24px;
      height: 24px;
      background: #4AD2BA;
      border-radius: 100%;
      font-size: 14px;
      color: #FFFFFF;
      line-height: 24px;
    }
  }
  .answer .user {
    span:first-of-type {
      background: #5093E1;
    }
  }
  .text {
    margin-left: 38px;
    font-size: 14px;
    line-height: 24px;
    color: #333333;
    text-align: left;
  }
}



