.operator-manage .first-line .form-group {
  display: inline-block;
  width: 100%;
  margin-bottom: 0;
}
.operator-manage .first-line .content-top {
  padding-top: 6px;
  padding-bottom: 6px;
}
.operator-manage .form-group .input-item {
  min-width: 280px;
}
.operator-manage .form-group .input-item .label-normal {
  width: 84px;
}
.operator-manage .search {
  padding-top: 6px;
}
.operator-manage .action-item {
  padding: 6px 0 6px 38px;
  text-align: left;
}
.operator-manage .icon {
  margin-right: 12px;
}
.operator-manage a {
  outline: none;
}
.operator-manage .list-width {
  padding: 8px 0;
}
.search,
.add-more {
  display: inline-block;
  float: right;
}
.module-body .cloud-ui-table thead .operatorMore {
  padding-left: 94px;
  text-align: left;
}



