@import "~variable";
@import "~mixins";

.page-template {
  background-color: @bgPage;
  .template-header {
    margin: 0 0 20px 0;
    padding: 50px;
    text-align: center;
    h1 {
      font-weight: bolder;
    }
  }
  .template-body {
    padding: 0 30px;
    width: 100%;
    height: 100%;
    h4 {
      margin-top: 50px;
      margin-bottom: 20px;
      font-weight: bolder;
    }
  }

  .template-footer {

  }
}



