.accredit-modal {
  width: 604px;
  padding: 10px 5px 5px 5px;
}
.accredit-modal .left-box .left-content {
  width: 100%;
  height: 350px;
  margin-bottom: 20px;
  border: solid 1px #d7dfe1;
}
.accredit-modal .left-box .tree-view {
  height: 348px;
}
.accredit-modal .left-box .form-title,
.accredit-modal .right-box .form-title {
  padding-bottom: 10px;
}
.accredit-modal .box-head {
  width: 100%;
  height: 60px;
}
.accredit-modal .box-head li {
  width: 50%;
  float: left;
  padding: 2px;
}
.accredit-modal .box-middle .input-search {
  display: inline-block;
  margin-top: 11px;
}
.accredit-modal .box-middle .form-group {
  margin-top: 10px;
  height: 22px;
  line-height: 22px;
  vertical-align: middle;
}
.accredit-modal .box-middle .form-checkbox {
  vertical-align: middle;
}
.accredit-modal .box-footer {
  display: inline-block;
  width: 100%;
  margin-top: 5px;
  border-top: solid 1px #d7dfe1;
}
.accredit-modal .box-footer ul {
  padding-left: 40px;
}
.accredit-modal .box-footer li {
  height: 40px;
  line-height: 40px;
  padding-top: 10px;
}
.accredit-modal .box-footer .icon {
  font-size: 24px;
  height: 25px;
}
.accredit-modal .box-footer .icon-error {
  color: #f65177;
}
.accredit-modal .box-footer .icon-correct {
  color: #50c14e;
}
.accredit-modal .box-footer .icon-query {
  color: #9fa4b4;
}
.accredit-modal .box-footer .form-title {
  padding-bottom: 0;
}
.accredit-modal .overbrim {
  width: 60px;
  overflow: hidden;
  display: inline-block;
  white-space: nowrap;
  text-overflow: ellipsis;
  float: right;
}



