@import "~variable";
@import "~mixins";

.section-aside-nav {

  //展开样式
  .aside-nav .nav-item {
    background-color: @bgSideNav;
  }
  .aside-nav .nav-item a {
    display: block;
    padding: 17px 0 15px 21px;
    text-align: left;
    height: 55px;
    color: @asideNavColor;
    .clearfix();
  }
  .aside-nav .nav-item a .module-arrow {
    float: right;
    margin-right: 10px;
    font-size: 16px;
  }
  .aside-nav .nav-item > a:hover,
  .aside-nav .nav-item > a:focus,
  .aside-nav .nav-item > a:hover > i,
  .aside-nav .nav-item > a:focus > i,
  .aside-nav .nav-item > .open-menu:hover,
  .aside-nav .nav-item > .open-menu:focus,
  .aside-nav .nav-item > .open-menu.selected {
    color: @fontWhite;
    background-color: @asideNavHover;
    .iconfont {
      color: @fontWhite;
    }
  }
  .aside-nav .nav-item > a > i {
    font-size: 20px;
    color: @asideNavIcon;
  }
  .aside-nav .nav-item .open-menu > i,
  .aside-nav .nav-item .open-menu .module-arrow {
    vertical-align: middle;
  }
  .item-detail {
    padding-left: 23px;
    span {
      font-family: PingFangSC-Regular;
    }
  }
  .aside-nav .nav-item > a {
    .icon-operation-mgr,
    .icon-push {
      font-size: 18px;
    }
    .icon-client-mgr {
      font-size: 20px;
    }
    .icon-store {
      font-size: 22px;
    }
    .icon-pull-down {
      color: @btnIconDown;
    }
  }
  .item-name {
    padding-left: 5px;
    font-family: PingFangSC-Regular;
  }

  //控制下拉icon旋转
  .aside-nav .nav-item .open-menu .module-arrow .icon-pull-down {
    transition-duration: .3s;
    transform: rotate(-90deg);
  }
  .aside-nav .nav-item .open-menu.selected .module-arrow .icon-pull-down {
    transition-duration: .3s;
    transform: rotate(0deg);
  }

  //是否显示三级菜单
  .aside-nav .nav-item .item-menu {
    display: none;
  }
  .aside-nav .nav-item .item-menu.selected {
    display: block;
  }

  .aside-nav .nav-item .item-menu .module-item .item-name:before {
    width: 7px;
    height: 7px;
    content: '';
    position: relative;
    left: -5px;
    display: inline-block;
    border: 1px solid #8ca0ab;
    border-radius: 50%;
  }
  .aside-nav .nav-item .item-menu .module-item a {
    padding-left: 60px;
    background-color: @bgSideNav;
    font-family: PingFangSC-Regular;
  }
  .aside-nav .nav-item .item-menu .module-item a:hover,
  .aside-nav .nav-item .item-menu .module-item a:focus,
  .aside-nav .nav-item .item-menu .module-item.active a {
    color: @fontWhite;
    background-color: @asideNavHover;
    font-family: PingFangSC-Regular;
    .item-name {
      padding-left: 4px;
      font-family: PingFangSC-Regular;
    }
    .item-name:before {
      border-color: @fontWhite;
    }
  }

  //收缩样式
  .fold-side .nav-item {
    background-color: @bgSideNav;
  }
  .fold-side .nav-item a {
    display: block;
    padding: 15px 5px 5px 5px;;
    text-align: center;
    height: 55px;
    color: @asideNavColor;
  }
  .fold-side .nav-item a:hover,
  .fold-side .nav-item a:focus {
    color: @fontWhite;
    background-color: @asideNavHover;
    .icon {
      color: @fontWhite;
    }
  }
  .fold-side .nav-item a .icon {
    color: @headerNavColor;
  }
}



