.branch-manage {
  background-color: #fff;
  border: solid 1px #e2e4ea;
}
.branch-manage .form-base {
  border: none;
  margin-bottom: 0;
}
.branch-manage .save {
  text-align: right;
}
.branch-manage .group-table {
  display: inline-block;
  width: 100%;
}
.branch-manage .box-left,
.branch-manage .box-right {
  height: 100%;
  min-height: 400px;
  border: 1px solid #d7dfe1;
}
.branch-manage .box-left {
  width: 40%;
  float: left;
}
.branch-manage .box-right {
  width: 59%;
  float: right;
}
.branch-manage .box-title {
  height: 32px;
  line-height: 32px;
  padding-left: 18px;
  background-color: #e0e2e8;
  color: #151E25;
  font-family: STHeitiSC-Light;
}
.branch-manage .box-content {
  height: 332px;
  overflow: auto;
}
.branch-manage .box-footer {
  height: 66px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-top: solid 1px #d7dfe1;
}
.branch-manage .btn-green {
  margin-left: 10px;
}
.branch-manage .branch-ul {
  width: 55%;
  height: 30px;
  line-height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.branch-manage .branch-ul li {
  width: 33%;
  height: 32px;
  line-height: 32px;
  float: left;
  color: #364242;
}
.branch-manage .icon-state {
  font-size: 18px;
  padding-right: 8px;
}
.branch-manage .icon-correct {
  color: #50c14e;
}
.branch-manage .icon-error {
  color: #f65177;
}
.branch-manage .icon-query {
  color: #9fa4b4;
}



