@import "~variable";
@import "~mixins";

.home {
  width: 100%;
  min-width: 1090px;
  height: auto;
  min-height: 100%;
  background-color: @bgPage;

  //侧边栏展开时样式
  .left-side {
    position: fixed;
    width: 200px;
    height: 100%;
    background-color: @bgSideNav;
    z-index: 100;
  }
  .left-side .left-div {
    height: 100%;
    overflow-y: auto;
  }
  .left-side .left-div .logo {
    position: fixed;
    width: 200px;
    height: 64px;
    line-height: 64px;
    background-color: @asideNavColor;
    z-index: 10;
  }
  .left-side .left-div .logo .logo-link {
    display: block;
    font-size: 16px;
    color: @fontWhite;
    text-align: center;
    text-decoration: none;
    font-family: PingFangSC-Regular;
  }
  .left-side .left-div .shrink-btn {
    position: fixed;
    top: 64px;
    width: 200px;
    height: 36px;
    background-color: @bgSideNav;
    z-index: 10;
  }
  .left-side .left-div .shrink-btn i.fold {
    margin-left: 150px;
  }
  .left-side .left-div .shrink-btn:hover .icon {
    color: @fontWhite;
  }
  .left-side .left-div .side-menu {
    margin-top: 100px;
    background-color: @clientBorder;
  }

  //侧边栏折叠时样式
  .left-side.unfold-side {
    width: 50px;
  }
  .left-side.unfold-side .left-div .logo {
    width: 50px;
  }
  .left-side.unfold-side .left-div .shrink-btn {
    width: 50px;
  }
  .left-side.unfold-side .left-div .shrink-btn i.unfold {
    margin: 0;
  }

  //侧边栏展开时右侧内容样式
  .main-content {
    padding-left: 200px;
    height: 100%;
    background-color: @tableTrHover;
    min-width: 1280px;
    overflow-x: scroll;
  }
  .main-content .top-menu {
    position: relative;
    width: 100%;
    height: 64px;
    background-color: @asideNavColor;
    z-index: 10;
    .menu-pool {
      display: inline-block;
      max-width: 70%;
      height: 64px;
      overflow-x: scroll;
      white-space: nowrap; 
    }
  }
  .main-content .top-menu .menu-btn {
    margin-top: 13px;
    margin-right: 10px;
    padding: 0;
    width: 130px;
    height: 38px;
    line-height: 38px;
    border-radius: 5px;
    background-color: @asideNavColor;
    color: @headerNavColor;
    font-size: 16px;
    letter-spacing: 0;
    font-family: STHeitiSC-Light;
  }
  .menu-btn .iconfont {
    font-size: 22px;
    vertical-align: middle;
  }
  .main-content .top-menu .exit,
  .main-content .top-menu .user {
    float: right;
    margin-top: 13px;
    padding: 0;
    width: 120px;
    height: 38px;
    line-height: 38px;
    background-color: @asideNavColor;
    color: @headerNavColor;
    .icon {
      margin-right: 8px;
    }
    span {
      display: inline-block;
      vertical-align: middle;
      max-width: 90px;
      text-align: left;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }
  .main-content .top-menu .exit:hover,
  .main-content .top-menu .user:hover {
    color: @fontWhite;
    .icon {
      color: @fontWhite;
    }
  }
  .main-content .top-menu .menu-btn .icon {
    margin-top: -4px;
    margin-right: 5px;
    color: @headerNavColor;
    font-size: 24px;
    padding-top: 2px;
  }
  .main-content .top-menu .menu-btn:hover,
  .main-content .top-menu .menu-btn:focus,
  .main-content .top-menu .menu-btn.selected {
    background-color: @btnBlue;
    color: @fontWhite;
    .icon {
      color: @fontWhite;
    }
  }

  //侧边栏折叠时右侧内容样式
  .main-content.unfold-content {
    padding-left: 50px;
  }
  .main-content.unfold-content .top-menu .exit {
    margin-right: 0;
  }

  //用户信息提示窗样式
  .popover-text {
    .unit {
      display: inline-block;
      width: 100px;
      text-align: right;
      vertical-align: middle;
    }
    .content {
      display: inline-block;
      width: 100px;
      vertical-align: middle;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
    margin-bottom: 5px;
    font-weight: 300;
  }
  .icon-userinfo,
  .icon-logout {
    font-size: 20px;
  }
}



