@import "~variable";
@import "~mixins";

.operator-edit {
  .form-group {
    padding-top: 15px;
  }
  .modal-footer {
    margin-top: 20px;
  }

  .error {
    padding: 0;
    font-size: 14px;
  }
  .error-tips {
    padding-left: 126px;
    p {
      text-align: left;
    }
  }
  .textarea-normal {
    display: inline-block;
    width: 81%;
    .form-control {
      float: left;
      margin-left: 4px;
      height: 46px;
    }
  }
  .col-sm-12 {
    label {
      float: left;
    }
  }
}




