@import "~variable";
@import "~mixins";

.crumbs {
  display: block;
  margin-left: 12px;
  height: 34px;
  li {
    display: inline;
    a,a:link,a:visited {
      color: @fontBase;
      display: block;
      float: left;
      font-size: 12px;
      margin-left: -13px;
      padding: 7px 17px 11px 25px;
      position: relative;
      text-decoration: none;
    }
    a {
      background-image: url(../../images/bg-crumbs.png);
      background-repeat: no-repeat;
      background-position: 100% 0;
      position: relative;
    }
    a:hover {
      color: @bgToaster;
      background-position: 100% -48px;
      cursor: pointer;
    }
    a:active {
      color: @bgToaster;
      background-position: 100% -96px;
    }
  }
  .nav-first{
    a {
      z-index:9;
    }
  }
  .nav-second {
    a {
      z-index:8;
    }
  }
  .nav-third {
    a {
      z-index:7;
    }
  }
  .first {
    padding-left: 8px;
    a {
      span {
        height: 29px;
        width: 3px;
        border-left: 1px solid @crumbsLine;
        position: absolute;
        top: 0px;
        left: 0px;
      }
    }
  }
}



