@import "~variable";
@import "~mixins";

.resetPassword {
  .oper-top {
    margin: 0;
    .input-item,
    .search {
      padding-top: 6px;
      padding-bottom: 6px;
    }
    .input-item:first-of-type {
      margin-right: 30px;
    }
  }
  .first-line {
    margin-bottom: 0;
    .input-item {
      height: 36px;
    }
    .input-normal {
      height: 35px;
      line-height: 35px;
      color: @clientTable;
    }
    .label-normal {
      float: left;
    }
  }
  .input-item .label-normal {
    width: 94px;
  }
  .input-item {
    min-width: 280px;
  }
  .error {
    padding: 0;
    font-size: 14px;
  }
  .error-tips {
    padding-left: 126px;
    p {
      text-align: left;
    }
  }
  .new-password {
    margin-right: 30px;
    float: left;
    .error-tips {
      padding-left: 100px;
    }
  }
  .form-title {
    padding-bottom: 13px;
  }
}



