.product-authority-manage .session-title {
  display: inline-block;
  margin: 10px 20px 20px 0;
  font-size: 16px;
  color: #151E25;
  cursor: pointer;
}
.product-authority-manage .session-title.active {
  color: #5093E1;
}
.product-authority-manage .form-group.date-height {
  height: 35px;
}
.product-authority-manage .form-title {
  overflow: hidden;
  padding-top: 10px;
  padding-bottom: 0;
}
.product-authority-manage .form-title .btn-normal {
  margin-left: 10px;
  float: right;
}
.product-authority-manage .date-box {
  padding-top: 6px;
}
.product-authority-manage .date-box label {
  padding: 0;
  margin: 0;
  width: 120px;
  height: 35px;
  line-height: 35px;
  text-align: right;
}
.product-authority-manage .date-box .date-input-normal {
  margin-left: 3px;
  margin-right: 5px;
}
.product-authority-manage .form-title {
  overflow: hidden;
  padding-bottom: 0;
}
.product-authority-manage .form-title .search-item {
  margin-right: 20px;
  float: right;
  width: 250px;
}
.product-authority-manage .form-title .search-item .input-normal {
  width: 250px;
}
.product-authority-manage .cloud-ui-table .more p {
  margin-right: 0;
  max-width: 200px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.product-authority-manage .icon-ascending {
  margin-left: 3px;
  margin-bottom: 2px;
  transform: scale(0.8, 1);
}
.product-authority-manage .btn-sequence {
  cursor: pointer;
}



