@import "~variable";
@import "~mixins";

.combine-online {
  .form-group {
    overflow: hidden;
    .input-item {
      min-width: 200px;
    }
    .form-control {
      display: inline-block;
    }
    .combine-right {
      width: 100px;
    }
    .btn-search {
      float: right;
      display: inline-block;
      padding: 5px 40px;
      border-radius: 4px;
      background-color: #5093e1;
      line-height: 25px;
      font-size: 16px;
      color: #fff;
    }
  }
  .table-responsive {
    max-width: 700px;
  }
  .cloud-ui-table {
    border-right: none;
  }
  .cloud-ui-table tbody td {
    border-right: 1px solid #ddd;
    line-height: 30px;
  }
  .cloud-ui-table thead th,
  .cloud-ui-table tbody td {
    text-align: center;
  }
  th.list-width {
    overflow: visible;
  }
  .selected-block {
    top: -7px;
  }
  .selected-block button {
    padding: 0;
    text-align: center;
    font-size: 14px;
    color: #070707;
    background-color: transparent;
    border: none;
    box-shadow: none;
  }
  .selected-block button i {
    font-size: 18px;
    margin-left: -10px;
    vertical-align: -2px;
  }
  .selected-block .select-dropdown {
    padding: 0;
    min-width: 125px;
    border-radius: 0;
    &:before {
      display: block;
      position: absolute;
      right: 55px;
      top: -11px;
      content: "";
      width: 0;
      height: 0;
      border-width: 5px;
      border-style: solid;
      border-color: transparent transparent #ddd transparent;
    }
    &:after {
      display: block;
      position: absolute;
      right: 55px;
      top: -10px;
      content: "";
      width: 0;
      height: 0;
      border-width: 5px;
      border-style: solid;
      border-color: transparent transparent white transparent;
    }
  }
  .selected-block .select-dropdown li {
    height: 35px;
    text-align: center;
    &:not(:last-of-type) {
      border-bottom: 1px solid #ddd;
    }
  }
  .selected-block .select-dropdown li a {
    line-height: 28px;
    &:hover {
      background-color: #5093e1;
      color: #fff;
    }
  }
  .cloud-ui-table tbody td .btn-status {
    padding: 2px 15px;
    line-height: 20px;
    font-size: 14px;
    color: #fff;
    border-radius: 4px;
    background-color: #b2b7c2;
  }
  .cloud-ui-table tbody td button.online {
    background-color: #5093e1;
  }
  .no-data {
    text-align: center;
  }
  .btn-page {
    float: right;
    margin-bottom: 20px;
    button {
      padding: 0 10px;
      border: 1px solid #ddd;
      color: #666;
      border-radius: 3px;
      font-size: 12px;
      line-height: 22px;
    }
    button:hover {
      background-color: #5093e1;
      color: @fontWhite;
      border-color: @fontWhite;
    }
    .pre {
      margin-right: 10px;
    }
    .disable,
    .disable:hover {
      background-color: #ddd;
      color: #666;
      border-color: #ddd;
      cursor: not-allowed;
    }
  }
}


