    margin-right: 5px;
    width: 150px;
    text-align: right;
  }
  .btn-normal {
    float: right;
  }
  .form-title {
    overflow: hidden;
    .record-date {
      margin: 7px 10px 0 0;
      float: right;
    }
  }
  .table .all {
    font-size: 16px;
    color: #D54043;
    text-align: center;
    background-color: rgba(232, 74, 77, 0.1);
  }
}



