@import "~variable";
@import "~mixins";

.edit-loading {
  div {
    margin: 0 auto;
  }
  .choose-picture {
    position: relative;
    width: 497px;
    height: 153px;
    span {
      position: relative;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    i {
      font-size: 80px;
      color: @fontWhite;
      border-radius: 50%;
      background-color: @btnIconDown;
    }
    input {
      position: absolute;
      width: 136px;
      height: 70px;
      opacity: 0;
    }
    img {
      position: absolute;
      width: 100%;
      height: 100%;
    }
  }
  .edit-advert {
    margin-top: 28px;
  }
  .form-group {
    .input-item {
      label {
        text-align: left;
        width: 84px;
      }
      .input-normal {
        width: 410px;
      }
    }
  }
  .error-tips {
    p {
      padding: 0 0 0 90px;
      font-size: 14px;
      text-align: left;
    }
  }
  .pic-gray {
    background-color: @borderInputColor;
  }
}




