.down-modal {
  padding: 15px;
  width: 600px;
  max-height: 460px;
  min-height: 150px;
  overflow: auto;
}
.down-modal .modal-footer {
  margin-top: 20px;
}
.down-modal .modal-footer .btn-normal {
  padding: 0;
}
.down-modal .template-modal span {
  display: inline-block;
  width: 100%;
  height: 80px;
  line-height: 80px;
  text-align: center;
  font-size: 18px;
}
.down-modal a {
  color: #fff;
  display: block;
}
.down-modal .btn-grey {
  background-color: #ddd;
  cursor: not-allowed;
}
.down-modal .btn-grey a {
  cursor: not-allowed;
}



