@import "~variable";
@import "~mixins";

.add-bg {
  div {
    margin: 0 auto;
  }
  .choose-picture {
    position: relative;
    width: 497px;
    height: 153px;
    span {
      position: relative;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    i {
      font-size: 80px;
      color: @fontWhite;
      border-radius: 50%;
      // background-color: @btnIconDown;
    }
    input {
      position: absolute;
      width: 136px;
      height: 70px;
      opacity: 0;
    }
    img {
      position: absolute;
      width: 100%;
      height: 100%;
    }
  }
  .pic-gray {
    background-color: @borderInputColor;
  }
}




