.dayCourse {
  max-height: 300px;
  overflow: scroll;
}
.dayCourse .title {
  margin: 20px 0;
  padding: 0;
  font-size: 18px;
  color: #333333;
  text-align: center;
}
.dayCourse .course {
  position: relative;
  padding-left: 12px;
  border-left: 1px solid #4BBDF9;
}
.dayCourse .course:before {
  position: absolute;
  top: -5px;
  left: -5px;
  content: ' ';
  width: 10px;
  height: 10px;
  background-color: #4BBDF9;
  border-radius: 100%;
}
.dayCourse .course .time {
  padding: 0;
  text-align: left;
  font-size: 12px;
  color: #999999;
}
.dayCourse .course .info {
  padding: 15px 0;
  text-align: left;
}
.dayCourse .course .info span:first-of-type {
  margin-right: 60px;
  display: inline-block;
  font-size: 12px;
  color: #1F242B;
  letter-spacing: 0;
  line-height: 14px;
}
.dayCourse .course .info span:last-of-type {
  display: inline-block;
  font-size: 12px;
  color: #333333;
  line-height: 14px;
}



