@import "~variable";
@import "~mixins";

.storeManage {
  .first-line {
    .form-group {
      display: inline-block;
      width: 100%;
      margin-bottom: 6px;
    }
  }
  .form-group .input-item {
    min-width: 270px;
    padding-top: 6px;
    .label-normal {
      width: 75px;
    }
  }
  .search {
    padding-top: 6px;
  }
  .add-more .input-normal {
    display: inline-block;
  }
  .cloud-ui-table .store-tag .tag-content {
    max-width: 200px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    text-align: center;
  }
  .cloud-ui-table tr {
    cursor: pointer;
  }
  .cloud-ui-table tr:hover {
    background-color: rgba(80,147,225, .3);
  }
  .cloud-ui-table .blue {
    background-color: rgba(80,147,225, .3);
  }
  .no-data {
    text-align: center;
    margin-top: 20px;
  }
  .icon {
    margin-right: 20px;
  }
}



