@import "~variable";
@import "~mixins";

.log-detail {
  .form-group {
    padding-top: 15px;
  }
  .modal-footer {
    margin-top: 20px;
  }

  .error {
    padding: 0;
    font-size: 14px;
  }
  .error-tips {
    padding-left: 126px;
    p {
      text-align: left;
    }
  }
  .textarea-normal {
    display: inline-block;
    width: 81%;
    .form-control {
      float: left;
      margin-left: 4px;
      height: 46px;
    }
  }
  .col-sm-12 {
    label {
      float: left;
    }
  }
  .date {
    position: relative;
    span {
      position: absolute;
      left: 10px;
      top: 0;
      height: 35px;
      line-height: 35px;
      font-size: 14px;
      color: #555;
    }
  }
}




