@import "~variable";
@import "~mixins";

.header-operate div.popover .popover-inner .popover-content {
  font-weight: normal;
  color: @fontBase;
}
.default-modal .modal-sm {
  width: 100%;
  margin: 0;
}
.modal-dialog {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  .modal-content {
    border-radius: 4px;
    width: auto;
    min-width: 445px;
  }
  .modal-header {
    border-radius: 4px 4px 0 0;
  }
  .icon-error {
    font-size: 24px;
  }
  .template-modal {
    font-size: 14px;
  }
  .modal-footer {
    padding-bottom: 32px;
  }
  p {
    text-align: center;
    padding: 30px 0;
    font-size: 18px;
  }
}



