.user-info-modal .line {
  margin-bottom: 10px;
}
.user-info-modal .line p {
  display: inline-block;
  margin-left: 60px;
  padding: 0;
  width: 100px;
  text-align: right;
  vertical-align: middle;
  font-size: 14px;
  color: #999999;
}
.user-info-modal .line span {
  display: inline-block;
  width: 150px;
  vertical-align: middle;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color: #666;
}



