@import "~variable";
@import "~mixins";

.sign-protocol-manage {
  .form-group.date-height {
    height: 35px;
  }
  .date-box {
    padding-top: 6px;
    label {
      padding: 0;
      margin: 0;
      width: 120px;
      height: 35px;
      line-height: 35px;
      text-align: right;
    }
    .date-input-normal {
      margin-left: 3px;
      margin-right: 5px;
    }
  }
  .form-title {
    overflow: hidden;
    padding-top: 10px;
    padding-bottom: 0;
    .btn-normal {
      margin-left: 10px;
      float: right;
    }
  }
  .cloud-ui-table {
    min-width: 2000px;
    overflow-x: auto;
    .operate {
      width: 200px;
    }
  }
}



