.download-progress-modal {
  width: 600px;
  /* 进度条容器默认样式 */
}
.download-progress-modal h4 {
  margin-bottom: 10px;
  text-align: center;
  font-weight: 400;
}
.download-progress-modal .bg_box {
  position: relative;
  width: 410px;
  height: 19px;
  margin: 0 auto;
  border-radius: 19px;
  overflow: hidden;
  background-color: #EAEEF5;
}
.download-progress-modal .bg_default {
  /* 动画时长1s */
  animation-duration: 10s;
  /*当动画完成后，保持最后一个属性值（在最后一个关键帧中定义）*/
  /* animation-fill-mode: forwards;  */
  /* 动画次数 无限次 */
  /* 执行动画名称 */
  animation-name: animationa;
  /* 动画速度，线性匀速 */
  animation-timing-function: linear;
  /* 背景颜色 */
  /* background: linear-gradient(to right, #fff 0%, #fff 50%, transparent 50%, transparent 100%); */
  background: #5093E1;
  /* 背景大小: 这里是容器大小 */
  width: 100%;
  height: 15px;
  border-radius: 15px;
  position: absolute;
  top: 2px;
}
@keyframes animationa {
  /* 修改用作背景的进度条容器定位 */
  0% {
    /* background-position: -410px 0; */
    left: -410px;
  }
  100% {
    /* background-position: 410px 0 */
    left: 0px;
  }
}



