@import "~variable";
@import "~mixins";

.order-info-qry {
  .date-box {
    display: inline-block;
  }
  .date-label-normal {
    width: 100px;
    text-align: right;
  }
  .date-input-normal {
    display: inline-block;
    position: relative;
    width: 190px;
  }
  .form-group .input-item {
    min-width: auto;
  }
  .form-group .input-item .label-normal {
    width: 100px;
    text-align: right;
  }
  .form-group {
    position: relative;
  }
  .form-group .btn-normal {
    float: right;
    margin-top: 3px;
  }
  .form-group.operate {
    overflow: hidden;
  }
  .cloud-ui-table {
    min-width: 2000px;
    overflow-x: auto;
  }
  .cloud-ui-pagination {
    text-align: right;
  }
}



