.liveMonitorContent .head {
  overflow: hidden;
  position: relative;
  margin-bottom: 0;
}
.liveMonitorContent .head .search-item {
  position: absolute;
  top: 0;
  right: 0;
}
.liveMonitorContent .title {
  margin-bottom: 0;
  padding: 12px;
  overflow: hidden;
  border: 1px solid #EFEFEF;
  background: #F6F7FA;
  border-radius: 5px 5px 0 0;
  position: relative;
  text-align: right;
}
.liveMonitorContent .title h4 {
  display: inline-block;
  line-height: 36px;
  font-size: 18px;
  font-weight: bold;
  color: #333333;
  float: left;
}
.liveMonitorContent .title span {
  display: block;
  padding-top: 10px;
  padding-right: 25px;
  color: #5093E1;
  font-size: 14px;
}
.liveMonitorContent .title span .icon {
  padding-right: 10px;
  font-size: 16px;
}
.liveMonitorContent .tab-live-change {
  padding-left: 35px;
  height: 45px;
  line-height: 45px;
  border-bottom: 1px solid #EFEFEF;
}
.liveMonitorContent .tab-live-change span {
  display: inline-block;
  font-size: 16px;
  margin-right: 40px;
  cursor: pointer;
}
.liveMonitorContent .tab-live-change span.tab-active {
  font-size: 18px;
  color: #5093E1;
}
.liveMonitorContent .content {
  margin-bottom: 0;
  width: 100%;
  border: 1px solid #EFEFEF;
}
.liveMonitorContent .content .tab-live-content,
.liveMonitorContent .content .right {
  display: inline-block;
  height: 500px;
}
.liveMonitorContent .content .tab-live-content {
  padding: 15px;
  width: 80%;
  overflow-y: auto;
}
.liveMonitorContent .content .tab-live-content .section {
  margin-bottom: 25px;
}
.liveMonitorContent .content .tab-live-content .user {
  margin-bottom: 10px;
  height: 23px;
}
.liveMonitorContent .content .tab-live-content .user img {
  margin-right: 10px;
  width: 23px;
  height: 23px;
  line-height: 23px;
  border-radius: 100%;
}
.liveMonitorContent .content .tab-live-content .user strong {
  margin-right: 10px;
  line-height: 23px;
  font-size: 14px;
  font-weight: bold;
  color: #333333;
}
.liveMonitorContent .content .tab-live-content .user span {
  line-height: 23px;
  font-size: 14px;
  color: #999999;
}
.liveMonitorContent .content .tab-live-content .detail {
  display: inline-block;
  margin-left: 30px;
  padding: 10px 12px;
  position: relative;
  background: #F6F7FA;
  border-radius: 3px;
  box-sizing: border-box;
}
.liveMonitorContent .content .tab-live-content .detail .delete {
  cursor: pointer;
  padding-left: 4px;
  position: absolute;
  right: -5px;
  bottom: -5px;
  width: 14px;
  height: 14px;
  line-height: 13px;
  color: white;
  font-size: 12px;
  font-weight: 400;
  background-color: rgba(0, 0, 0, 0.3);
  border-radius: 100%;
}
.liveMonitorContent .content .tab-live-content .detail .text {
  margin-bottom: 5px;
  position: relative;
  font-size: 14px;
  color: #333333;
  background: #F6F7FA;
  border-radius: 3px;
}
.liveMonitorContent .content .tab-live-content .detail .img {
  margin-bottom: 5px;
  position: relative;
  width: 120px;
  height: 80px;
  background: #F6F7FA;
  border-radius: 3px;
  box-sizing: border-box;
}
.liveMonitorContent .content .tab-live-content .detail .img .image {
  width: 100%;
  height: 100%;
}
.liveMonitorContent .content .tab-live-content .detail .combine {
  margin-bottom: 5px;
  display: inline-block;
  padding: 10px;
  width: 320px;
  height: 170px;
  background: white;
  border-radius: 3px;
  box-sizing: border-box;
}
.liveMonitorContent .content .tab-live-content .detail .combine .name {
  margin-bottom: 10px;
  font-size: 14px;
  color: #333333;
}
.liveMonitorContent .content .tab-live-content .detail .combine .risk,
.liveMonitorContent .content .tab-live-content .detail .combine .tag {
  display: inline-block;
  margin-bottom: 18px;
  padding: 1px 2px;
  font-size: 12px;
  border-radius: 3px;
  color: #999999;
  border: 1px solid #999999;
}
.liveMonitorContent .content .tab-live-content .detail .combine .risk.red,
.liveMonitorContent .content .tab-live-content .detail .combine .tag.red {
  color: #FF0000;
  border: 1px solid #FF0000;
}
.liveMonitorContent .content .tab-live-content .detail .combine .risk.orange,
.liveMonitorContent .content .tab-live-content .detail .combine .tag.orange {
  color: #FF8866;
  border: 1px solid #FF8866;
}
.liveMonitorContent .content .tab-live-content .detail .combine .risk.blue,
.liveMonitorContent .content .tab-live-content .detail .combine .tag.blue {
  color: #66B1FF;
  border: 1px solid #66B1FF;
}
.liveMonitorContent .content .tab-live-content .detail .combine .income {
  margin-bottom: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.liveMonitorContent .content .tab-live-content .detail .combine .income .item strong {
  display: block;
  font-size: 24px;
  font-weight: bold;
  color: #333333;
  line-height: 33px;
}
.liveMonitorContent .content .tab-live-content .detail .combine .income .item em {
  margin-left: 3px;
  font-size: 16px;
  font-weight: bold;
}
.liveMonitorContent .content .tab-live-content .detail .combine .income .item p {
  font-size: 12px;
  color: #999999;
  text-align: center;
}
.liveMonitorContent .content .tab-live-content .detail .combine .time {
  text-align: left;
  font-size: 12px;
  color: #999999;
}
.liveMonitorContent .content .tab-live-content .bottom {
  margin-top: 50px;
}
.liveMonitorContent .content .tab-live-content .bottom button {
  margin: 0 auto;
  padding: 5px 10px;
  display: block;
  border: 1px solid #ddd;
  color: #999;
  border-radius: 4px;
}
.liveMonitorContent .content .right {
  width: 19%;
  vertical-align: top;
}
.liveMonitorContent .content .right p {
  margin: 15px;
  text-align: center;
  font-size: 16px;
  font-weight: bold;
  color: #666666;
}
.liveMonitorContent .content .right .holder {
  text-align: center;
}
.liveMonitorContent .content .right .holder img {
  margin-right: 10px;
  width: 23px;
  height: 23px;
  border-radius: 100%;
}
.liveMonitorContent .content .right .holder strong {
  font-size: 14px;
  font-weight: bold;
  color: #333333;
}
.liveMonitorContent .no-data {
  padding: 10px;
  text-align: center;
}



