.sensitiveWords .form-group {
  overflow: hidden;
}
.sensitiveWords .form-group .pull-right {
  margin-left: 10px;
}
.sensitiveWords .cloud-ui-table th.little {
  width: 50px;
}
.sensitiveWords .cloud-ui-table .left {
  width: 200px;
  text-align: left;
}
.sensitiveWords .cloud-ui-table .remark {
  display: inline-block;
  max-width: 250px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.sensitiveWords .cloud-ui-table td a {
  outline: none;
}
.sensitiveWords .form-group .dropdown-menu li {
  width: 188px;
}
.sensitiveWords .import-excel {
  display: none;
}
.sensitiveWords .select-btn {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  display: inline-block;
  width: 151px;
}
.sensitiveWords .select-list {
  padding-right: 15px;
  width: 188px;
  overflow: hidden;
  display: inline-block;
  text-overflow: ellipsis;
}
.sensitiveWords .select-dropdown {
  max-height: 300px;
  overflow: auto;
}
.sensitiveWords .no-data {
  padding: 5px 0;
  text-align: center;
  font-size: 16px;
}



