.roles-manage {
  padding: 0;
}
.roles-manage .first-line {
  padding-top: 5px;
  padding-bottom: 11px;
}
.roles-manage .first-line .form-group {
  position: relative;
}
.roles-manage .first-line .roles-search {
  position: absolute;
  bottom: 0;
  right: 0;
}
.roles-manage .first-line .input-item {
  padding-top: 21px;
}
.roles-manage .input-item .label-normal {
  width: 115px;
}
.roles-manage i {
  margin-right: 13px;
}
.roles-manage .oper-type {
  padding-left: 20px;
}
.roles-manage a {
  outline: none;
}
.roles-manage .icon {
  margin-right: 20px;
}
.cloud-ui-pagination {
  text-align: right;
}
.centre-head,
.centre-content {
  width: 100%;
  border: solid 1px #d7dfe1;
  background-color: #fff;
}
.centre-head {
  height: 155px;
  margin-bottom: 10px;
  padding-right: 20px;
  padding-left: 5px;
}
.centre-head .form-group {
  padding: 20px 0 0 0;
  margin-bottom: 0;
}
.centre-head .input-item {
  float: right;
}
.centre-head button {
  float: right;
}
.centre-content {
  padding: 20px;
}
.centre-content .col-sm-4 {
  padding-top: 15px;
}
.centre-content .min-table {
  width: 100%;
  min-height: 205px;
  position: relative;
  display: inline-block;
}
.centre-content .form-title {
  padding: 0 0 0 5px;
  margin-top: 3px;
}
.centre-content button {
  float: right;
}
.centre-content .icon-primary {
  margin-right: 10px;
}
.centre-content .cloud-ui-pagination {
  position: absolute;
  right: 0;
  bottom: 0;
  margin-top: 0;
}



