@import "~variable";
@import "~mixins";

.operator-accredit {
  padding: 0;
  height: 460px;
  .form-group {
    padding-top: 15px;
  }
  .form-group .tree-view {
    max-height: 250px;
  }
  .modal-footer {
    margin-top: 20px;
  }
  .template-modal {
    padding: 0 10px;
  }
  .box-left,
  .box-right {
    width: 287px;
    height: 284px;
    margin: 3px;
    padding: 0;
    border: solid 1px @borderColor;
  }
  .list-gray {
    height: 32px;
    background-color: @listHeader;
    span {
      padding-left: 10px;
      height: 32px;
      line-height: 32px;
      color: @listHeaderFont;
    }
  }
  .form-title {
    padding-bottom: 0;
  }
  .overbrim {
    width: 102px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    float: right;
  }
  .col-sm-4 {
    padding-right: 0;
    label {
      width: 74px;
      text-align: right;
    }
  }
}




