@import "~variable";
@import "~mixins";

.live-bg-setting {
  .form-title {
    line-height: 32px;
  }
  .form-title .btn-normal {
    float: right;
  }
  .form-title .input-state {
    margin-left: 15px;
    font-size: 12px;
    color: @btnRed;
  }
  .swiper-container {
    // width: 100%;
    // height: 232px;
    .swiper-wrapper {
      margin: 0 auto;
      width: 90%;
    }
    .swiper-slide {
      margin: 0 10px;
      padding: 6px;
      width: 321px!important;
      height: 153px;
      text-align: center;
      background: #F7F7F7;
      img {
        width: 309px;
        height: 101px;
        background-size: 100% 100%;
        background-repeat: no-repeat;
      }
      .operate {
        position: relative;
        margin-top: 10px;
        text-align: right;
      }
      .operate img {
        position: absolute;
        left: 0;
        top: 0;
        width: 60px;
        height: 23px;
      }
      .operate i {
        margin-left: 5px;
        width: 25px;
        height: 25px;
        border-radius: 3px;
        font-size: 18px;
        color: white;
        &.gray {
          background: #A7A7A7;
        }
        &.red {
          background: #F65177;
        }
        &.yellow {
          background: #FFBD24;
        }
      }
    }
    .swiper-button-prev,
    .swiper-button-next {
      background: none;
    }
  }
}


