@import "~variable";
@import "~mixins";

.new-menu-demo {
  .form-group {
    margin-bottom: 0;
    overflow: hidden;
  }
  .form-group .tip {
    margin-top: 5px;
    margin-right: 10px;
    float: right;
    color: #151E25;
  }
  .form-group .add {
    float: right;
  }
  .form-group em {
    color: #F65177;
    font-weight: normal;
  }
  .modal-content .form-group .input-item label {
    width: auto;
  }
  .intro {
    margin-right: 20px;
    line-height: 35px;
    float: right;
    font-size: 14px;
    color: #5093E1;
    cursor: pointer;
    em {
      margin-right: 5px;
      padding: 0 4px;
      width: 14px;
      height: 14px;
      border-radius: 100%;
      font-size: 12px;
      color: #5093E1;
      border: 1px solid #5093E1;
    }
  }
}



