@import "~variable";
@import "~mixins";

.viewpoint-detail-modal {
  width: 600px;
  .top {
    padding: 5px 0;
    border-bottom: 1px solid #ddd;
    h4 {
      margin-bottom: 5px;
      line-height: 20px;
      font-size: 18px;
      font-weight: bold;
      color: #333333;
      letter-spacing: 0;
      line-height: 14px;
    }
    p {
      padding: 5px 0;
      text-align: left;
      line-height: 14px;
      font-size: 12px;
      color: #999999;
      letter-spacing: 0;
    }
  }
  .detail {
    // margin: 0 auto;
    padding: 10px 0;
    max-width: 600px;
    max-height: 400px;
    overflow: auto;
    p {
      text-align: left;
      padding: 0;
    }
  }
  .form-group .input-item .input-normal .text-area {
    width: 190px;
  }
}




