@import "~variable";
@import "~mixins";

.liveMonitorContent {
  .head {
    overflow: hidden;
    position: relative;
    margin-bottom: 0;
    .search-item {
      position: absolute;
      top: 0;
      right: 0;
    }
  }
  .title {
    margin-bottom: 0;
    padding: 12px;
    overflow: hidden;
    border: 1px solid #EFEFEF;
    background: #F6F7FA;
    border-radius: 5px 5px 0 0;
    position: relative;
    text-align: right;
    h4 {
      display: inline-block;
      line-height: 36px;
      font-size: 18px;
      font-weight: bold;
      color: #333333;
      float: left;
    }
    span {
      display: block;
      padding-top: 10px;
      padding-right: 25px;
      color: #5093E1;
      font-size: 14px;
      .icon {
        padding-right: 10px;
        font-size: 16px;
      }
    }
  }
  .tab-live-change {
    padding-left: 35px;
    height: 45px;
    line-height: 45px;
    border-bottom: 1px solid #EFEFEF;
    span {
      display: inline-block;
      font-size: 16px;
      margin-right: 40px;
      cursor: pointer;
      &.tab-active {
        font-size: 18px;
        color: #5093E1;
      }
    }
  }
  .content {
    margin-bottom: 0;
    width: 100%;
    border: 1px solid #EFEFEF;
    .tab-live-content,
    .right {
      display: inline-block;
      height: 500px;
    }
    .tab-live-content {
      padding: 15px;
      width: 80%;
      overflow-y: auto;
      .section {
        margin-bottom: 25px;
      }
      .user {
        margin-bottom: 10px;
        height: 23px;
      }
      .user img {
        margin-right: 10px;
        width: 23px;
        height: 23px;
        line-height: 23px;
        border-radius: 100%;
      }
      .user strong {
        margin-right: 10px;
        line-height: 23px;
        font-size: 14px;
        font-weight: bold;
        color: #333333;
      }
      .user span {
        line-height: 23px;
        font-size: 14px;
        color: #999999;
      }
      .detail {
        display: inline-block;
        margin-left: 30px;
        padding: 10px 12px;
        position: relative;
        background: #F6F7FA;
        border-radius: 3px;
        box-sizing: border-box;
        .delete {
          cursor: pointer;
          padding-left: 4px;
          position: absolute;
          right: -5px;
          bottom: -5px;
          width: 14px;
          height: 14px;
          line-height: 13px;
          color: white;
          font-size: 12px;
          font-weight: 400;
          background-color: rgba(0, 0, 0, 0.3);
          border-radius: 100%;
        }
      }
      .detail .text {
        margin-bottom: 5px;
        position: relative;
        font-size: 14px;
        color: #333333;
        background: #F6F7FA;
        border-radius: 3px;
      }
      .detail .img {
        margin-bottom: 5px;
        position: relative;
        width: 120px;
        height: 80px;
        background: #F6F7FA;
        border-radius: 3px;
        box-sizing: border-box;
        .image {
          width: 100%;
          height: 100%;
        }
      }
      .detail .combine {
        margin-bottom: 5px;
        display: inline-block;
        padding: 10px;
        width: 320px;
        height: 170px;
        background: white;
        border-radius: 3px;
        box-sizing: border-box;
        .name {
          margin-bottom: 10px;
          font-size: 14px;
          color: #333333;
        }
        .risk,
        .tag {
          display: inline-block;
          margin-bottom: 18px;
          padding: 1px 2px;
          font-size: 12px;
          border-radius: 3px;
          color: #999999;
          border: 1px solid #999999;
          &.red {
            color: #FF0000;
            border: 1px solid #FF0000;
          }
          &.orange {
            color: #FF8866;
            border: 1px solid #FF8866;
          }
          &.blue {
            color: #66B1FF;
            border: 1px solid #66B1FF;
          }
        }
        .income {
          margin-bottom: 10px;
          display: flex;
          justify-content: space-between;
          align-items: center;
        }
        .income .item strong {
          display: block;
          font-size: 24px;
          font-weight: bold;
          color: #333333;
          line-height: 33px;
        }
        .income .item em {
          margin-left: 3px;
          font-size: 16px;
          font-weight: bold;
        }
        .income .item p {
          text-align: center;
          font-size: 12px;
          color: #999999;
          text-align: center;
        }
        .time {
          text-align: left;
          font-size: 12px;
          color: #999999;
        }
      }
      .bottom {
        margin-top: 50px;
        button {
          margin: 0 auto;
          padding: 5px 10px;
          display: block;
          border: 1px solid #ddd;
          color: #999;
          border-radius: 4px;
        }
      }
    }
    .right {
      width: 19%;
      vertical-align: top;
      p {
        margin: 15px;
        text-align: center;
        font-size: 16px;
        font-weight: bold;
        color: #666666;
      }
      .holder {
        text-align: center;
      }
      .holder img {
        margin-right: 10px;
        width: 23px;
        height: 23px;
        border-radius: 100%;
      }
      .holder strong {
        font-size: 14px;
        font-weight: bold;
        color: #333333;
      }
    }
  }
  .no-data {
    padding: 10px;
    text-align: center;
  }
}



