.live-bg-setting .form-title {
  line-height: 32px;
}
.live-bg-setting .form-title .btn-normal {
  float: right;
}
.live-bg-setting .form-title .input-state {
  margin-left: 15px;
  font-size: 12px;
  color: #f65177;
}
.live-bg-setting .swiper-container .swiper-wrapper {
  margin: 0 auto;
  width: 90%;
}
.live-bg-setting .swiper-container .swiper-slide {
  margin: 0 10px;
  padding: 6px;
  width: 321px!important;
  height: 153px;
  text-align: center;
  background: #F7F7F7;
}
.live-bg-setting .swiper-container .swiper-slide img {
  width: 309px;
  height: 101px;
  background-size: 100% 100%;
  background-repeat: no-repeat;
}
.live-bg-setting .swiper-container .swiper-slide .operate {
  position: relative;
  margin-top: 10px;
  text-align: right;
}
.live-bg-setting .swiper-container .swiper-slide .operate img {
  position: absolute;
  left: 0;
  top: 0;
  width: 60px;
  height: 23px;
}
.live-bg-setting .swiper-container .swiper-slide .operate i {
  margin-left: 5px;
  width: 25px;
  height: 25px;
  border-radius: 3px;
  font-size: 18px;
  color: white;
}
.live-bg-setting .swiper-container .swiper-slide .operate i.gray {
  background: #A7A7A7;
}
.live-bg-setting .swiper-container .swiper-slide .operate i.red {
  background: #F65177;
}
.live-bg-setting .swiper-container .swiper-slide .operate i.yellow {
  background: #FFBD24;
}
.live-bg-setting .swiper-container .swiper-button-prev,
.live-bg-setting .swiper-container .swiper-button-next {
  background: none;
}



