.home {
  width: 100%;
  min-width: 1090px;
  height: auto;
  min-height: 100%;
  background-color: #f4f5f9;
}
.home .left-side {
  position: fixed;
  width: 200px;
  height: 100%;
  background-color: #e2e4ea;
  z-index: 100;
}
.home .left-side .left-div {
  height: 100%;
  overflow-y: auto;
}
.home .left-side .left-div .logo {
  position: fixed;
  width: 200px;
  height: 64px;
  line-height: 64px;
  background-color: #333333;
  z-index: 10;
}
.home .left-side .left-div .logo .logo-link {
  display: block;
  font-size: 16px;
  color: #fff;
  text-align: center;
  text-decoration: none;
  font-family: PingFangSC-Regular;
}
.home .left-side .left-div .shrink-btn {
  position: fixed;
  top: 64px;
  width: 200px;
  height: 36px;
  background-color: #e2e4ea;
  z-index: 10;
}
.home .left-side .left-div .shrink-btn i.fold {
  margin-left: 150px;
}
.home .left-side .left-div .shrink-btn:hover .icon {
  color: #fff;
}
.home .left-side .left-div .side-menu {
  margin-top: 100px;
  background-color: #f6f6f6;
}
.home .left-side.unfold-side {
  width: 50px;
}
.home .left-side.unfold-side .left-div .logo {
  width: 50px;
}
.home .left-side.unfold-side .left-div .shrink-btn {
  width: 50px;
}
.home .left-side.unfold-side .left-div .shrink-btn i.unfold {
  margin: 0;
}
.home .main-content {
  padding-left: 200px;
  height: 100%;
  background-color: #f6f7fa;
  min-width: 1280px;
  overflow-x: scroll;
}
.home .main-content .top-menu {
  position: relative;
  width: 100%;
  height: 64px;
  background-color: #333333;
  z-index: 10;
}
.home .main-content .top-menu .menu-pool {
  display: inline-block;
  max-width: 70%;
  height: 64px;
  overflow-x: scroll;
  white-space: nowrap;
}
.home .main-content .top-menu .menu-btn {
  margin-top: 13px;
  margin-right: 10px;
  padding: 0;
  width: 130px;
  height: 38px;
  line-height: 38px;
  border-radius: 5px;
  background-color: #333333;
  color: #9fa4b4;
  font-size: 16px;
  letter-spacing: 0;
  font-family: STHeitiSC-Light;
}
.home .menu-btn .iconfont {
  font-size: 22px;
  vertical-align: middle;
}
.home .main-content .top-menu .exit,
.home .main-content .top-menu .user {
  float: right;
  margin-top: 13px;
  padding: 0;
  width: 120px;
  height: 38px;
  line-height: 38px;
  background-color: #333333;
  color: #9fa4b4;
}
.home .main-content .top-menu .exit .icon,
.home .main-content .top-menu .user .icon {
  margin-right: 8px;
}
.home .main-content .top-menu .exit span,
.home .main-content .top-menu .user span {
  display: inline-block;
  vertical-align: middle;
  max-width: 90px;
  text-align: left;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.home .main-content .top-menu .exit:hover,
.home .main-content .top-menu .user:hover {
  color: #fff;
}
.home .main-content .top-menu .exit:hover .icon,
.home .main-content .top-menu .user:hover .icon {
  color: #fff;
}
.home .main-content .top-menu .menu-btn .icon {
  margin-top: -4px;
  margin-right: 5px;
  color: #9fa4b4;
  font-size: 24px;
  padding-top: 2px;
}
.home .main-content .top-menu .menu-btn:hover,
.home .main-content .top-menu .menu-btn:focus,
.home .main-content .top-menu .menu-btn.selected {
  background-color: #5093e1;
  color: #fff;
}
.home .main-content .top-menu .menu-btn:hover .icon,
.home .main-content .top-menu .menu-btn:focus .icon,
.home .main-content .top-menu .menu-btn.selected .icon {
  color: #fff;
}
.home .main-content.unfold-content {
  padding-left: 50px;
}
.home .main-content.unfold-content .top-menu .exit {
  margin-right: 0;
}
.home .popover-text {
  margin-bottom: 5px;
  font-weight: 300;
}
.home .popover-text .unit {
  display: inline-block;
  width: 100px;
  text-align: right;
  vertical-align: middle;
}
.home .popover-text .content {
  display: inline-block;
  width: 100px;
  vertical-align: middle;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.home .icon-userinfo,
.home .icon-logout {
  font-size: 20px;
}



