.subscribe-modal {
  padding: 15px;
  width: 600px;
  max-height: 460px;
  min-height: 150px;
  overflow: auto;
}
.subscribe-modal .form-group {
  padding-top: 15px;
}
.subscribe-modal .modal-footer {
  margin-top: 20px;
}
.subscribe-modal .sub-list {
  max-height: 200px;
  overflow: auto;
  text-align: center;
}
.subscribe-modal th {
  text-align: center;
}
.subscribe-modal .form-title {
  padding-bottom: 0;
}
.subscribe-modal .list-thirty {
  float: left;
}



