@import "~variable";
@import "~mixins";

.tag-manage-modal {
  .tag-add .form-horizontal .form-group .input-item {
    margin-left: 40px;
  }
  .tag-add .form-horizontal .form-group .input-item .input-normal {
    margin-right: 20px;
  }
  .tag-pool {
    margin-top: 20px;
    max-width: 385px;
  }
  .tag-pool .btn-normal {
    margin-right: 20px;
    margin-bottom: 20px;
  }
  .tag-pool .btn-tag {
    position: relative;
    padding-right: 25px;
    max-width: 100%;
    word-break: break-all;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    border: 1px solid @borderColor;
    color: @fontBase;
    cursor: default;
  }
  .tag-pool .btn-tag .icon {
    position: absolute;
    top: 7px;
    right: 8px;
    border-radius: 50%;
    background-color: @asideNavIcon;
    color: @fontWhite;
  }
}



