@import "~variable";
@import "~mixins";

.operator-frozen {
  .form-group {
    padding-top: 15px;
  }
  .modal-footer {
    margin-top: 20px;
  }
  .textarea-normal {
    display: inline-block;
    width: 81%;
    .form-control {
      float: left;
      margin-left: 4px;
      height: 46px;
    }
  }
  .col-sm-12 {
    label {
      float: left;
    }
  }
}




