.stockPoolManage .form-group {
  margin-bottom: 0;
}
.stockPoolManage .form-header {
  display: inline-block;
}
.stockPoolManage .tips {
  display: inline-block;
  margin-left: 10px;
  font-size: 12px;
}
.stockPoolManage .tips em {
  color: #f65177;
  font-weight: normal;
}
.stockPoolManage .search-item {
  float: right;
  margin-right: 20px;
}
.stockPoolManage .cloud-ui-table .red {
  color: #f65177;
}
.stockPoolManage .cloud-ui-table .green {
  color: #50c14e;
}
.stockPoolManage .cloud-ui-table .delete {
  cursor: pointer;
}
.stockPoolManage .cloud-ui-pagination {
  text-align: right;
}



