@import "~variable";
@import "~mixins";

.basic-value-setting {
  .line-title {
    margin-right: 30px;
  }
  .form-group {
    margin-bottom: 0;
  }
  .form-group.content {
    padding-left: 15px;
    margin-bottom: 20px;
  }
  .form-radio {
    margin-right: 10px;
    vertical-align: middle;
  }
  .radio-title {
    margin-right: 10px;
  }
  .input-item {
    min-width: 250px;
  }
  .input-item.short {
    position: relative;
    min-width: 100px;
    .input-normal {
      width: 100px;
    }
  }
  .line {
    margin-right: 30px;
    &::after {
      content: '';
      position: absolute;
      right: -26px;
      top: 17px;
      width: 18px;
      height: 1px;
      background-color: #E2E4EA;
    }
  }
  .btn-normal {
    margin-left: 50px;
  }
}



