.add-stock-modal .form-group .input-item label {
  width: auto;
  text-align: left;
}
.add-stock-modal .search .btn-normal {
  cursor: pointer;
}
.add-stock-modal .search .btn-normal.disabled {
  cursor: wait;
  background-color: #A6A6A6;
}
.add-stock-modal .cloud-ui-table {
  margin: 0;
  max-width: 1000px;
  border: none;
}
.add-stock-modal .cloud-ui-table thead tr th {
  text-align: center;
  color: #A6A6A6;
  border-bottom: none !important;
  background-color: transparent;
}
.add-stock-modal .cloud-ui-table tbody tr td {
  text-align: center;
}
.add-stock-modal .cloud-ui-table .blue {
  background-color: rgba(80, 147, 225, 0.3);
}
.add-stock-modal .cloud-ui-table .red {
  color: #f65177;
}
.add-stock-modal .cloud-ui-table .green {
  color: #50c14e;
}
.add-stock-modal .cloud-ui-pagination {
  margin-top: 0;
}
.add-stock-modal .cloud-ui-pagination .pagination {
  margin-bottom: 0;
}



