@import "~variable";
@import "~mixins";

.branch-manage {
  background-color: @fontWhite;
  border: solid 1px @bgSideNav;
  .form-base {
    border: none;
    margin-bottom: 0;
  }
  .save {
    text-align: right;
  }
  .group-table {
    display: inline-block;
    width: 100%;
  }
  .box{
    &-left,
    &-right {
      height: 100%;
      min-height: 400px;
      border: 1px solid @borderColor;
    }
    &-left {
      width: 40%;
      float: left;
    }
    &-right {
      width: 59%;
      float: right;
    }
    &-title {
      height: 32px;
      line-height: 32px;
      padding-left: 18px;
      background-color: @bgTitle;
      color: @bgTitleColor;
      font-family: STHeitiSC-Light;
    }
    &-content {
      height: 332px;
      overflow: auto;
    }
    &-footer {
      height: 66px;
      display: flex;
      justify-content: center;
      align-items: center;
      border-top: solid 1px @borderColor;
    }
  }
  .btn-green {
    margin-left: 10px;
  }
  .branch-ul {
    width: 55%;
    height: 30px;
    line-height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    li {
      width: 33%;
      height: 32px;
      line-height: 32px;
      float: left;
      color: @crumbsfont;
    }
  }
  .icon-state {
    font-size: 18px;
    padding-right: 8px;
  }
  .icon-correct {
    color: @btnGreen;
  }
  .icon-error {
    color: @btnRed;
  }
  .icon-query {
    color: @headerNavColor;
  }
}









