@import "~variable";
@import "~mixins";

.add-new-invest {
  h4 {
    margin: 30px 5px;
    font-size: 14px;
    font-weight: normal;
  }
  .form-base .form-group .error-tips {
    padding: 0;
    font-size: 12px;
    color: @errorColor;
  }
  .form-group {
    padding-bottom: 20px;
    margin-bottom: 0;
    .input-item {
      position: relative;
    }
  }
  .group-table {
    display: inline-block;
    width: 100%;
  }
  .box-left {
    float: left;
    width: 40%;
    height: 100%;
    min-height: 400px;
    border: 1px solid @borderColor;
  }
  .box-right {
    float: right;
    width: 59%;
    height: 100%;
    min-height: 400px;
    border: 1px solid @borderColor;
  }
  .box-title {
    padding-left: 18px;
    height: 32px;
    line-height: 32px;
    background-color: @bgTitle;
    color: @bgTitleColor;
    em {
      vertical-align: middle;
      font-weight: normal;
      font-size: 20px;
      color: @errorColor;
    }
  }
  .box-content {
    min-height: 300px;
  }
  .error {
    padding-left: 126px;
  }
  .error-tips {
    position: absolute;
  }
}



