@import "~variable";
@import "~mixins";

.down-modal {
  padding: 15px;
  width: 600px;
  max-height: 460px;
  min-height: 150px;
  overflow: auto;
  .modal-footer {
    margin-top: 20px;
    .btn-normal {
      padding: 0;
    }
  }
  .template-modal {
    span {
      display: inline-block;
      width: 100%;
      height: 80px;
      line-height: 80px;
      text-align: center;
      font-size: 18px;
    }
  }
  a {
    color: @fontWhite;
    display: block;
  }
  .btn-grey {
    background-color: @tagBorder;
    cursor: not-allowed;
    a {
      cursor: not-allowed;
    }
  }
}




