@import "~variable";
@import "~mixins";

.log-inquiry {
  .first-line {
    .form-group {
      display: inline-block;
      width: 100%;
      position: relative;
    }
  }
  .line-head {
    width: 70%;
    min-width: 571px;
    float: left;
  }
  .form-group .input-item {
    margin-right: 30px;
    padding-top: 14px;
    min-width: 283px;
    .label-normal {
      width: 86px;
      text-align: right;
    }
  }
  .search {
    display: inline-block;
    float: right;
    padding-top: 20px;
    width: 30%;
    position: absolute;
    bottom: 0;
    text-align: right;
  }
  .cloud-ui-pagination {
    text-align: right;
  }
  .form-title {
    padding-bottom: 14px;
  }
  .date-box {
    padding-top: 6px;
    label {
      padding: 0;
      margin: 0;
      width: 86px;
      height: 35px;
      line-height: 35px;
      text-align: right;
    }
    .date-input-normal {
      margin-left: 3px;
    }
  }
  select {
    color: @asideNavColor;
  }
  .icon {
    color: @btnGray;
  }
}



