@import "~variable";
@import "~mixins";

.roles-manage {
  padding: 0;
  .first-line {
    padding-top: 5px;
    padding-bottom: 11px;
    .form-group {
      position: relative;
    }
    .roles-search {
      position: absolute;
      bottom: 0;
      right: 0;
    }
    .input-item {
      padding-top: 21px;
    }
  }
  .input-item {
    .label-normal {
      width: 115px;
    }
  }
  i {
    margin-right: 13px;
  }
  // .icon-key {
  //   font-size: 12px;
  // }
  .oper-type {
    padding-left: 20px;
  }
  a {
    outline: none;
  }
  .icon {
    margin-right: 20px;
  }
}
.cloud-ui-pagination {
  text-align: right;
}
.centre {
  &-head,
  &-content {
    width: 100%;
    border: solid 1px @borderColor;
    background-color: @fontWhite;
  }
  &-head {
    height: 155px;
    margin-bottom: 10px;
    padding-right: 20px;
    padding-left: 5px;
    .form-group {
      padding: 20px 0 0 0;
      margin-bottom: 0;
    }
    .input-item {
      float: right;
    }
    button {
      float: right;
    }
  }
  &-content {
    padding: 20px;
    .col-sm-4 {
      padding-top: 15px;
    }
    .min-table {
      width: 100%;
      min-height: 205px;
      position: relative;
      display: inline-block;
    }
    .form-title {
      padding: 0 0 0 5px;
      margin-top: 3px;
    }
    button {
      float: right;
    }
    .icon-primary {
      margin-right: 10px;
    }
    .cloud-ui-pagination {
      position: absolute;
      right: 0;
      bottom: 0;
      margin-top: 0;
    }
  }
}



