.edit-loading div {
  margin: 0 auto;
}
.edit-loading .choose-picture {
  position: relative;
  width: 497px;
  height: 153px;
}
.edit-loading .choose-picture span {
  position: relative;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.edit-loading .choose-picture i {
  font-size: 80px;
  color: #fff;
  border-radius: 50%;
  background-color: #9BA0B0;
}
.edit-loading .choose-picture input {
  position: absolute;
  width: 136px;
  height: 70px;
  opacity: 0;
}
.edit-loading .choose-picture img {
  position: absolute;
  width: 100%;
  height: 100%;
}
.edit-loading .edit-advert {
  margin-top: 28px;
}
.edit-loading .form-group .input-item label {
  text-align: left;
  width: 84px;
}
.edit-loading .form-group .input-item .input-normal {
  width: 410px;
}
.edit-loading .error-tips p {
  padding: 0 0 0 90px;
  font-size: 14px;
  text-align: left;
}
.edit-loading .pic-gray {
  background-color: #ccc;
}



