.protocol-introduce-modal {
  padding: 0 50px;
  width: 600px;
  height: 350px;
  overflow: auto;
}
.protocol-introduce-modal .session {
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.protocol-introduce-modal .session p {
  padding: 0;
  font-size: 18px;
  color: #666666;
  border-bottom: 2px solid transparent;
  cursor: pointer;
}
.protocol-introduce-modal .session .selected {
  position: relative;
  font-size: 18px;
  color: #5093E1;
  border-bottom: 2px solid #5093E1;
}
.protocol-introduce-modal .content p {
  margin-bottom: 5px;
  padding: 0;
  text-align: left;
  font-size: 14px;
  color: #333333;
}
.protocol-introduce-modal .content h3 {
  margin: 10px 0;
  font-size: 18px;
  font-weight: 400;
  color: #333333;
}
.protocol-introduce-modal .content strong {
  font-size: 14px;
  font-weight: 400;
  color: #E84342;
}
.protocol-introduce-modal .content table {
  margin-bottom: 10px;
}
.protocol-introduce-modal .content table td {
  padding: 10px;
  font-size: 14px;
  color: #999999;
  border: 1px solid #ddd;
}
.protocol-introduce-modal .content table td:first-of-type {
  background: #F6F6F6;
}
.protocol-introduce-modal .content table td:last-of-type {
  width: 250px;
}
.protocol-introduce-modal .content section {
  margin-bottom: 10px;
  padding: 5px;
  background: #F6F7FA;
}
.protocol-introduce-modal .content section p {
  text-align: left;
  font-size: 12px;
  color: #333333;
}



