@import "~variable";
@import "~mixins";

.product-authority-manage {
  .session-title {
    display: inline-block;
    margin: 10px 20px 20px 0;
    font-size: 16px;
    color: #151E25;
    cursor: pointer;
    &.active {
      color: #5093E1;
    }
  }
  .form-group.date-height {
    height: 35px;
  }
  .form-title {
    overflow: hidden;
    padding-top: 10px;
    padding-bottom: 0;
    .btn-normal {
      margin-left: 10px;
      float: right;
    }
  }
  .date-box {
    padding-top: 6px;
    label {
      padding: 0;
      margin: 0;
      width: 120px;
      height: 35px;
      line-height: 35px;
      text-align: right;
    }
    .date-input-normal {
      margin-left: 3px;
      margin-right: 5px;
    }
  }
  .form-title {
    overflow: hidden;
    padding-bottom: 0;
    .search-item {
      margin-right: 20px;
      float: right;
      width: 250px;
    }
    .search-item .input-normal {
      width: 250px;
    }
  }
  .cloud-ui-table .more p {
    margin-right: 0;
    max-width: 200px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .icon-ascending {
    margin-left: 3px;
    margin-bottom: 2px;
    transform: scale(.8, 1);
  }
  .btn-sequence {
    cursor: pointer;
  }
}



