@import "~variable";
@import "~mixins";

.cloud-ui-table {
  //table-layout: auto;
  table-layout: fixed;
  thead th {
    color: @fontTitle;
    font-weight: normal;
  }
  tr {
    border-bottom: none;
  }
  tbody tr td {
    border-right: none;
  }
}

.table-content {
  display: inline-block;
  width: 100%;
  margin-top: 20px;
  .col-sm-6 {
    padding: 0;
    height: 100%;
    position: relative;
  }
  .content-list {
    width: 100%;
  }
  .table-title {
    width: 100%;
    height: 36px;
    line-height: 36px;
    background-color: @bgSideNav;
    font-size: 14px;
    color: @asideNavColor;
    th {
      padding-left: 15px;
    }
  }
  .main-list {
    .list {
      height: 36px;
      line-height: 36px;
      border-bottom: solid 1px @bgSideNav;
    }
    .list:hover {
//      border-bottom: solid 1px @headerNavFocus;
      background-color: @headerNavFocus;
      color: @fontWhite;
      .arr {
        display: inline-block;
        right: -1px;
      }
    }
  }
  .arr {
    position: absolute;
    right: -1px;
    display: none;
    width: 0;
    height: 0;
    margin-top: 8px;
    border-top: 11px solid transparent;
    border-right: 11px solid @fontWhite;
    border-bottom: 11px solid transparent;
    z-index: 10;
  }
  .selected {
    border-bottom: solid 1px @headerNavFocus;
    background-color: @headerNavFocus;
    color: @fontWhite;
    .list {
      border-bottom: solid 1px @headerNavFocus;
    }
    .arr {
      display: inline-block;
      right: -1px;
    }
  }
  .table-gray {
    height: 36px;
    background-color: @bgSideNav;
  }
  .detail-content {
    height: 90%;
    overflow: auto;
    border-left: solid 1px @bgSideNav;
    border-bottom: solid 1px @bgSideNav;
    border-right: solid 1px @bgSideNav;
  }
  .table-detail {
    margin-left: -1px;
  }
}
.list-width {
  padding: 0 20px 0 15px;
  white-space:nowrap;
  text-overflow:ellipsis;
  overflow: hidden;
}
.list-forty {
  position: relative;
  width: 40%;
  float: left;
}
.list-thirty {
  width: 30%;
  float: right;
}
.list-name {
  width: 25%;
  float: left;
}
.list-twenty {
  width: 20%;
  float: left;
}
.list-type {
  width: 15%;
  float: left;
}
.list-ten {
  width: 10%;
  float: left;
}
.list-index {
  width: 5%;
  float: left;
}
.list-half {
  width: 50%;
  float: left;
}
.table-responsive {
  .table>thead>tr>th {
    border-bottom: none;
  }
}
.list-handle {
  a {
    outline: none;
  }
}
.list-arr {
  width: 23px;
  float: right;
  color: transparent;
}
.null-data {
  padding: 10px;
  display: inline-block;
  width: 100%;
  text-align: center;
}







