@import "~variable";
@import "~mixins";

.operator-add {
  .form-group {
    padding-top: 15px;
  }
  .form-group ul.dropdown-menu.branchList {
    width: 220px;
    height: 300px;
    overflow: scroll;
  }
  .modal-footer {
    margin-top: 20px;
  }

  .error {
    padding: 0;
    font-size: 14px;
  }
  .error-tips {
    padding-left: 126px;
    p {
      text-align: left;
    }
  }
  .textarea-normal {
    display: inline-block;
    width: 81%;
    .form-control {
      float: left;
      margin-left: 4px;
      height: 46px;
    }
  }
  .col-sm-12 {
    label {
      float: left;
    }
  }
}




