@import "~variable";
@import "~mixins";
.loginIn {
  width: 100%;
  height: 100%;
  background: -webkit-linear-gradient(left, @loginBg, @loginBgt);
}
.login {
  height: 100%;
  background: url('../../assets/images/bg.png') no-repeat;
//  background: -webkit-linear-gradient(left, @loginBg, @loginBgt);
  background-size: cover;
  .input-group {
    width: 100%;
  }
  .logo-box {
    width: auto;
    height: auto;
    position: absolute;
    top: 30px;
    left: 120px;
    img {
      width: 112px;
      height: 75px;
      background-repeat: no-repeat;
      background-size: cover;
    }
  }
  .login-box {
    position: absolute;
    top: 50%;
    margin-top: -230px;
    left: 50%;
    margin-left: -200px;
    padding: 50px 30px;
    text-align: center;
    width: 380px;
    height: 450px;
    box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.50);
    border-radius: 6px;
    background-color: @bgForm;
    h4 {
      margin-bottom: 10px;
      font-size: 22px;
      font-weight: normal;
    }
    .form-group {
      margin-bottom: 35px;
      position: relative;
    }
    .form-group.account .input-group .input-group-btn,
    .form-group.password .input-group .input-group-btn {
      background-color: #f4f4f4;
    }
    .form-group .input-group .input-group-btn i {
      cursor: default;
    }
    .form-group .input-group .input-group-btn .icon-client-mgr {
      font-size: 18px;
    }
    .form-group.verify-code .input-group .input-group-btn {
      width: 120px;
      border: none;
    }
    .form-group.verify-code .input-group .input-group-btn img {
      width: 100%;
      height: 100%;
      border-radius: 0 4px 4px 0;
      background-size: cover;
      background-repeat: no-repeat;
    }
    button {
      margin-top: 20px;
      width: 100%;
    }
  }
  .error-tips {
    position: absolute;
    padding-left: 10px;
  }
  .error-info {
    width: 100%;
    height: 20px;
    margin-bottom: 10px;
    color: @btnRed;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}



