@import "~variable";
@import "~mixins";

.combine-check {
  // 表头部分样式
  select {
    width: 120px;
    margin-left: 10px;
  }
  .form-title {
    padding-bottom: 14px;
    height: 40px;
  }
  .btn-online {
    padding: 5px 20px;
    line-height: 25px;
    background-color: #5093e1;
    border-radius: 4px;
    color: #fff;
    font-size: 14px;
  }
  .combine-right {
    float: right;
  }
  .input-item {
    min-width: 200px;
  }
  .btn-search {
    float: right;
    display: inline-block;
    padding: 5px 40px;
    border-radius: 4px;
    background-color: #5093e1;
    line-height: 25px;
    font-size: 16px;
    color: #fff;
  }

  // 表格部分样式
  .table-content {
    height: 460px;
    margin-top: 0;
    th {
      font-weight: 400;
    }
    th.list-width {
      overflow: visible;
    }
    .selected-block button {
      padding: 0;
      text-align: left;
      font-size: 14px;
      color: #333333;
      background-color: transparent;
      border: none;
      box-shadow: none;
    }
    .selected-block button i {
      font-size: 18px;
      margin-left: -3px;
      vertical-align: -2px;
    }
    .selected-block .select-dropdown {
      padding: 0;
      left: -25px;
      min-width: 110px;
      border-radius: 0;
      &:after {
        display: block;
        position: absolute;
        right: 48px;
        top: -10px;
        content: "";
        width: 0;
        height: 0;
        border-width: 5px;
        border-style: solid;
        border-color: transparent transparent white transparent;
      }
    }
    .selected-block .select-dropdown li {
      height: 35px;
      text-align: center;
      &:not(:last-of-type) {
        border-bottom: 1px solid #ddd;
      }
    }
    .selected-block .select-dropdown li a {
      line-height: 28px;
      &:hover {
        background-color: #5093e1;
        color: #fff;
      }
    }
  }
  // 左侧列表样式
  .table-list {
    .cloud-ui-pagination {
      position: absolute;
      bottom: 0;
      right: 4px;
    }
    .cloud-ui-pagination .pagination{
      margin: 5px;
    }
    label {
      font-weight: normal;
    }
    .content-list {
      .disable-msg {
        position: absolute;
        right: 0;
      }
      .colorRed {
        color: #F55077;
      }
      .colorGreen {
        color: #50C14E;
      }
    }
    .null-data {
      padding-top: 10px;
    }
  }
  // 右侧列表样式
  .table-detail {
    .detail-content {
      padding-top: 10px;
      padding-bottom: 20px;
    }
    .combine-title {
      position: relative;
      height: 30px;
      text-align: center;
      font-weight: 100;
    }
    .combine-title img {
      position: absolute;
      top: -10px;
      left: 0;
    }
    .btn-normal {
      float: right;
    }
    .btn-green {
      padding: 0;
    }
    .btn-green a {
      display: inline-block;
      width: 100%;
      height: 100%;
      color: @fontWhite;
    }
    .client-list .client-content {
      padding-left: 5px;
      word-wrap: break-word;
      white-space: inherit;
    }
    .client-list .detail-more {
      width: 130px;
    }
    .client-list .cloud-ui-table {
      display: inline-block;
    }
    .client-search {
      width: 210px;
    }
    .btn-red {
      margin-right: 10px;
    }
    .click-more {
      display: inline-block;
      width: 100%;
      height: 30px;
      text-align: center;
      cursor: default;
    }
    .history {
      height: 123px;
      margin-bottom: 10px;
      overflow: hidden;
    }
    .combiHis-table {
      display: inline-block;
      width: 100%;
      font-size: 12px;
      th,
      td {
        height: 25px;
        line-height: 25px;
        padding: 0 8px;
      }
      .color {
        color: @clientTable;
      }
      tr:hover {
        background-color: @tableTrHover;
      }
    }
    .combiHis-height {
      height: 25px;
      line-height: 25px;
    }
    .combiHis {
      .table>tbody>tr>td,
      .table>tbody+tbody {
        border-top: none;
      }
    }
    .selectedhis {
      background-color: @tableTrHover;
    }
    .hisPoint {
      cursor: pointer;
    }
    textarea {
      padding-left: 5px;
      width: 100%;
      height: 64px;
      border: solid 1px @borderColor;
      color: @clientTable;
    }
  }

  // 调整页面左右布局比例
  .col-sm-6.left {
    width: 55%;
  }
  .col-sm-6.right {
    width: 45%;
  }
  .client-info .input-item label,
  .client-info .input-item span {
    width: 50%;
  }
}



