.live-check .form-group {
  margin-bottom: 0;
}
.live-check .form-group .select-list {
  padding-right: 15px;
  width: 150px;
  overflow: hidden;
  display: inline-block;
  text-overflow: ellipsis;
}
.live-check .form-group .select-dropdown {
  max-height: 300px;
  overflow: auto;
}
.live-check .form-title {
  overflow: hidden;
}
.live-check .form-title .search-item {
  float: right;
}
.live-check .cloud-ui-table th {
  overflow: visible;
}
.live-check .cloud-ui-table th .selected-block button {
  padding: 0;
  text-align: left;
  font-size: 14px;
  color: #333333;
  background-color: transparent;
  border: none;
  box-shadow: none;
}
.live-check .cloud-ui-table th .selected-block button i {
  font-size: 18px;
  margin-left: -3px;
  vertical-align: -2px;
}
.live-check .cloud-ui-table th .selected-block .select-dropdown {
  padding: 0;
  min-width: 110px;
  border-radius: 0;
}
.live-check .cloud-ui-table th .selected-block .select-dropdown:after {
  display: block;
  position: absolute;
  right: 48px;
  top: -10px;
  content: "";
  width: 0;
  height: 0;
  border-width: 5px;
  border-style: solid;
  border-color: transparent transparent white transparent;
}
.live-check .cloud-ui-table th .selected-block .select-dropdown li {
  height: 35px;
  text-align: center;
}
.live-check .cloud-ui-table th .selected-block .select-dropdown li:not(:last-of-type) {
  border-bottom: 1px solid #ddd;
}
.live-check .cloud-ui-table th .selected-block .select-dropdown li a {
  line-height: 28px;
}
.live-check .cloud-ui-table th .selected-block .select-dropdown li a:hover {
  background-color: #5093e1;
  color: #fff;
}
.live-check .cloud-ui-table .colorRed {
  color: #F55077;
}
.live-check .cloud-ui-table .colorGreen {
  color: #50C14E;
}



