* {
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

html,
body {
  height: 100%;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Microsoft YaHei', 'Source Sans Pro', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  overflow-x: hidden;
  overflow-y: auto;
}

/* Layout */
.wrapper {
  .clearfix();
  height: 100%;
  position: relative;
  overflow-x: hidden;
  overflow-y: auto;
  padding: 0 15px;
}

input[type="checkbox"] {
  margin: 0;
  line-height: 30px;
}

// 日历
.uib-daypicker {
  outline: none;
}
.uib-datepicker-popup.dropdown-menu {
  padding: 2px;
}
.form-control {
  height: 35px;
}
.icon {
  font-size: 16px;
}
.bg-gray {
  &:active,
  &:hover {
    background-color: #DFD3D3;
  }
}

//重置label样式
.form-group label {
  font-weight: normal;
  color: #666;
}
//重置table的th样式
.module-body {
  .cloud-ui-table {
    text-align: center;
    thead th {
      color: @bgToaster;
      text-align: center;
    }
  }
}
.row {
  margin-left: 0;
  margin-right: 0;
}
.form-checkbox,
.form-checkbox label {
  width: 18px;
  height: 18px;
}
.padding-format {
  padding: 0;
}

// 重置分页样式
.cloud-ui-pagination .pagination>li>a {
  width: auto;
  min-width: 24px;
}

// 隐藏
.ele-hidden {
  display: none !important;
}



