.more-invest-info h4 {
  margin: 30px 5px;
  font-size: 14px;
  font-weight: normal;
}
.more-invest-info .form-group .text-content {
  display: inline-block;
  margin-bottom: 20px;
}
.more-invest-info .form-group .text-content label {
  width: 35%;
  height: 26px;
  line-height: 26px;
  min-width: 100px;
  text-align: right;
  color: #666;
}
.more-invest-info .form-group .text-content.col-sm-12 label {
  width: 120px;
}
.more-invest-info .form-group .text-content label em {
  font-size: 20px;
  font-weight: normal;
  vertical-align: middle;
  color: #f65177;
}
.more-invest-info .form-group .text-content span {
  width: 65%;
  height: 28px;
  line-height: 28px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  color: #333;
}



