.add-new-invest h4 {
  margin: 30px 5px;
  font-size: 14px;
  font-weight: normal;
}
.add-new-invest .form-base .form-group .error-tips {
  padding: 0;
  font-size: 12px;
  color: #ed6090;
}
.add-new-invest .form-group {
  padding-bottom: 20px;
  margin-bottom: 0;
}
.add-new-invest .form-group .input-item {
  position: relative;
}
.add-new-invest .group-table {
  display: inline-block;
  width: 100%;
}
.add-new-invest .box-left {
  float: left;
  width: 40%;
  height: 100%;
  min-height: 400px;
  border: 1px solid #d7dfe1;
}
.add-new-invest .box-right {
  float: right;
  width: 59%;
  height: 100%;
  min-height: 400px;
  border: 1px solid #d7dfe1;
}
.add-new-invest .box-title {
  padding-left: 18px;
  height: 32px;
  line-height: 32px;
  background-color: #e0e2e8;
  color: #151E25;
}
.add-new-invest .box-title em {
  vertical-align: middle;
  font-weight: normal;
  font-size: 20px;
  color: #ed6090;
}
.add-new-invest .box-content {
  min-height: 300px;
}
.add-new-invest .error {
  padding-left: 126px;
}
.add-new-invest .error-tips {
  position: absolute;
}



