@import "~variable";
@import "~mixins";

.edit-info-modal {
  .new-mobile .tip {
    padding: 0;
    margin-left: 125px;
    font-size: 12px;
    color: @fontBase;
  }
  .verify-code .input-normal {
    width: 120px;
  }
  .verify-code .btn-code {
    position: relative;
    top: 1px;
    width: 65px;
    height: 35px;
    border: 1px solid @btnGreen;
    border-radius: 4px;
    color: @btnGreen;
  }
  .remarks .label-normal {
    vertical-align: top;
  }
  .remarks .input-normal {
    position: relative;
    top: 10px;
  }
  .remarks .input-normal .form-control {
    height: 100px;
  }
}



