@import "~variable";
@import "~mixins";

.tree-view {
  margin-bottom: 20px;
  padding: 19px;
  //height: 350px;
  background-color: white;
  overflow-y: auto;
  overflow-x: auto;
  li {
    position: relative;
    list-style-type: none;
    white-space: nowrap;
  }
  li li {
    position: relative;
    margin: -10px 0 0 0;
    padding: 0 0 0 22px;
    list-style-type: none;
  }
  li li li {
    position: relative;
    margin: -10px 0 0 0;
    padding: 0 0 0 22px;
    list-style-type: none;
  }
  li li li li {
    position: relative;
    margin: -10px 0 0 0;
    padding: 0 0 0 22px;
    list-style-type: none;
  }
  ul {
    margin-top: 0;
  }
  ul ul {
    margin-top: 0;
  }
  ul ul ul {
    margin-top: 0;
  }
  ul ul ul ul {
    margin-top: 0;
  }
  span {
    border-radius: 5px;
    display: inline-block;
    border: 0 solid @clientTable;
    text-decoration: none;
    cursor: pointer
  }
  ul span {
    border-radius: 5px;
    border: 0 solid @clientTable;
    text-decoration: none;
  }
  li li li li span {
    border-radius: 5px;
    border: 0 solid @clientTable;
    text-decoration: none;
  }
  span span {
    cursor: pointer
  }
  li:last-child::before {
    height: 50px
  }
  li ul.line {
    background: url(../../../assets/images/shu.png) 0 0 repeat-y;
  }
  .icon-correct {
    color: @btnGreen !important;
  }
  .icon-error {
    color: @btnRed !important;
  }
  .icon-query {
    color: @btnGray !important;
  }
  .red {
    color: @btnRed;
  }
}



