@import "../variable";
@import "../mixins";

.default-modal {
  @media (min-width: 768px) {
    .modal-sm {
      width: 445px;
    }
  }
  .modal-content {
    width: 445px;
    box-shadow: none;
    border: none;
  }
  .modal-header {
    padding: 8px 15px;
    border-radius: 6px 6px 0 0;
    border-bottom: none;
    color: @fontWhite;
    background-color: @modalHeaderColor;
    .modal-title {
      line-height: 28px;
      font-size: 16px;
      font-weight: 300;
    }
    .btn-close {
      float: right;
      // line-height: 2;
    }
    .icon-tag-close {
      color: @fontWhite;
      font-size: 18px;
      cursor: pointer;
      // border: 1px solid @fontWhite;
      // border-radius: 50%;
    }
  }
  .modal-body {
    padding: 30px;
  }
  .modal-footer {
    border: none;
    text-align: center;
    padding-top: 0;
    button {
      margin: 0 10px;
    }
  }
}




