.operator-add .form-group {
  padding-top: 15px;
}
.operator-add .form-group ul.dropdown-menu.branchList {
  width: 220px;
  height: 300px;
  overflow: scroll;
}
.operator-add .modal-footer {
  margin-top: 20px;
}
.operator-add .error {
  padding: 0;
  font-size: 14px;
}
.operator-add .error-tips {
  padding-left: 126px;
}
.operator-add .error-tips p {
  text-align: left;
}
.operator-add .textarea-normal {
  display: inline-block;
  width: 81%;
}
.operator-add .textarea-normal .form-control {
  float: left;
  margin-left: 4px;
  height: 46px;
}
.operator-add .col-sm-12 label {
  float: left;
}



