@import "~variable";
@import "~mixins";

.accredit-modal {
  width: 604px;
  padding: 10px 5px 5px 5px;
  .left-box {
    .left-content {
      width: 100%;
      height: 350px;
      margin-bottom: 20px;
      border: solid 1px @borderColor;
    }
    .tree-view {
      height: 348px;
    }
  }
  .left-box,
  .right-box {
    .form-title {
    padding-bottom: 10px;
    }
  }
  .box-head {
    width: 100%;
    height: 60px;
    li {
      width: 50%;
      float: left;
      padding: 2px;
    }
  }
  .box-middle {
    .input-search {
      display: inline-block;
      margin-top: 11px;
    }
    .form-group {
      margin-top: 10px;
      height: 22px;
      line-height: 22px;
      vertical-align: middle;
    }
    .form-checkbox {
      vertical-align: middle;
    }
  }
  .box-footer {
    display: inline-block;
    width: 100%;
    margin-top: 5px;
    border-top: solid 1px @borderColor;
    ul {
      padding-left: 40px;
    }
    li {
      height: 40px;
      line-height: 40px;
      padding-top: 10px;
    }
    .icon {
      font-size: 24px;
      height: 25px;
    }
    .icon-error {
      color: @btnRed;
    }
    .icon-correct {
      color:  @btnGreen;
    }
    .icon-query {
      color: @btnGray;
    }
    .form-title {
      padding-bottom: 0;
    }
  }
  .overbrim {
    width: 60px;
    overflow: hidden;
    display: inline-block;
    white-space: nowrap;
    text-overflow: ellipsis;
    float: right;
  }
}




