.tag-manage-modal .tag-add .form-horizontal .form-group .input-item {
  margin-left: 40px;
}
.tag-manage-modal .tag-add .form-horizontal .form-group .input-item .input-normal {
  margin-right: 20px;
}
.tag-manage-modal .tag-pool {
  margin-top: 20px;
  max-width: 385px;
}
.tag-manage-modal .tag-pool .btn-normal {
  margin-right: 20px;
  margin-bottom: 20px;
}
.tag-manage-modal .tag-pool .btn-tag {
  position: relative;
  padding-right: 25px;
  max-width: 100%;
  word-break: break-all;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  border: 1px solid #d7dfe1;
  color: #666;
  cursor: default;
}
.tag-manage-modal .tag-pool .btn-tag .icon {
  position: absolute;
  top: 7px;
  right: 8px;
  border-radius: 50%;
  background-color: #9fa4b4;
  color: #fff;
}



