.loginIn {
  width: 100%;
  height: 100%;
  background: -webkit-linear-gradient(left, #009ede, #00b0a4);
}
.login {
  height: 100%;
  background: url('../../assets/images/bg.png') no-repeat;
  background-size: cover;
}
.login .input-group {
  width: 100%;
}
.login .logo-box {
  width: auto;
  height: auto;
  position: absolute;
  top: 30px;
  left: 120px;
}
.login .logo-box img {
  width: 112px;
  height: 75px;
  background-repeat: no-repeat;
  background-size: cover;
}
.login .login-box {
  position: absolute;
  top: 50%;
  margin-top: -230px;
  left: 50%;
  margin-left: -200px;
  padding: 50px 30px;
  text-align: center;
  width: 380px;
  height: 450px;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.5);
  border-radius: 6px;
  background-color: #fff;
}
.login .login-box h4 {
  margin-bottom: 10px;
  font-size: 22px;
  font-weight: normal;
}
.login .login-box .form-group {
  margin-bottom: 35px;
  position: relative;
}
.login .login-box .form-group.account .input-group .input-group-btn,
.login .login-box .form-group.password .input-group .input-group-btn {
  background-color: #f4f4f4;
}
.login .login-box .form-group .input-group .input-group-btn i {
  cursor: default;
}
.login .login-box .form-group .input-group .input-group-btn .icon-client-mgr {
  font-size: 18px;
}
.login .login-box .form-group.verify-code .input-group .input-group-btn {
  width: 120px;
  border: none;
}
.login .login-box .form-group.verify-code .input-group .input-group-btn img {
  width: 100%;
  height: 100%;
  border-radius: 0 4px 4px 0;
  background-size: cover;
  background-repeat: no-repeat;
}
.login .login-box button {
  margin-top: 20px;
  width: 100%;
}
.login .error-tips {
  position: absolute;
  padding-left: 10px;
}
.login .error-info {
  width: 100%;
  height: 20px;
  margin-bottom: 10px;
  color: #f65177;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}



