@import "~variable";
@import "~mixins";

.dayCourse {
  max-height: 300px;
  overflow: scroll;
  .title {
    margin: 20px 0;
    padding: 0;
    font-size: 18px;
    color: #333333;
    text-align: center;
  }
  .course {
    position: relative;
    padding-left: 12px;
    border-left: 1px solid #4BBDF9;
    &:before {
      position: absolute;
      top: -5px;
      left: -5px;
      content: ' ';
      width: 10px;
      height: 10px;
      background-color: #4BBDF9;
      border-radius: 100%;
    }
    .time {
      padding: 0;
      text-align: left;
      font-size: 12px;
      color: #999999;
    }
    .info {
      padding: 15px 0;
      text-align: left;
      span:first-of-type {
        margin-right: 60px;
        display: inline-block;
        font-size: 12px;
        color: #1F242B;
        letter-spacing: 0;
        line-height: 14px;
      }
      span:last-of-type {
        display: inline-block;
        font-size: 12px;
        color: #333333;
        line-height: 14px;
      }
    }
  }
}



