.basic-value-setting .line-title {
  margin-right: 30px;
}
.basic-value-setting .form-group {
  margin-bottom: 0;
}
.basic-value-setting .form-group.content {
  padding-left: 15px;
  margin-bottom: 20px;
}
.basic-value-setting .form-radio {
  margin-right: 10px;
  vertical-align: middle;
}
.basic-value-setting .radio-title {
  margin-right: 10px;
}
.basic-value-setting .input-item {
  min-width: 250px;
}
.basic-value-setting .input-item.short {
  position: relative;
  min-width: 100px;
}
.basic-value-setting .input-item.short .input-normal {
  width: 100px;
}
.basic-value-setting .line {
  margin-right: 30px;
}
.basic-value-setting .line::after {
  content: '';
  position: absolute;
  right: -26px;
  top: 17px;
  width: 18px;
  height: 1px;
  background-color: #E2E4EA;
}
.basic-value-setting .btn-normal {
  margin-left: 50px;
}



