.customerManage .search-item {
  float: right;
  margin-right: 20px;
}
.customerManage .table-content {
  height: 460px;
  margin-top: 0;
}
.customerManage .table-content .cloud-ui-pagination {
  position: absolute;
  bottom: 0;
  right: 4px;
}
.customerManage .table-content .detail-content {
  padding-top: 10px;
  padding-bottom: 20px;
}
.customerManage .table-content label {
  font-weight: normal;
}
.customerManage .table-content .input-item.ownership {
  padding-left: 53px;
}
.customerManage .table-content .input-item.phone {
  padding-left: 20px;
}
.customerManage .content-list .disable-msg {
  position: absolute;
  right: 0;
}
.customerManage .content-list .list-forty {
  padding-left: 30px;
}
.customerManage .cloud-ui-pagination .pagination {
  margin: 5px;
}
.customerManage .btn-normal {
  float: right;
}
.customerManage .form-title {
  padding-bottom: 14px;
}
.customerManage .icon-jinyan {
  color: #f65177;
}
.customerManage .btn-green {
  padding: 0;
}
.customerManage .btn-green a {
  display: inline-block;
  width: 100%;
  height: 100%;
  color: #fff;
}
.customerManage .client-list .cloud-ui-table tbody tr td {
  border-top: none;
}
.customerManage .client-list .cloud-ui-table tr:hover {
  background-color: #fff;
}
.customerManage .client-list .table {
  color: #666;
}
.customerManage .client-list .table > thead > tr > th {
  color: #999999;
  border-bottom: none;
}
.customerManage .client-list .list-thirty {
  float: left;
}
.client-avatar {
  width: 80px;
  height: 80px;
  margin: 0 auto;
  border-radius: 50%;
  background-color: #d8d8d8;
}
.client-intro {
  height: 26px;
  line-height: 26px;
  margin-top: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.client-name {
  padding-right: 8px;
}
.client-position {
  color: #3F3F3F;
  padding-right: 5px;
}
.client-symbol {
  width: 32px;
  height: 20px;
  line-height: 20px;
  text-align: center;
  background-color: #47B34F;
  border-radius: 7px;
  color: #fff;
  font-size: 12px;
}
.client-symbol-limit {
  width: 32px;
  height: 20px;
  line-height: 20px;
  text-align: center;
  background-color: #f65177;
  border-radius: 7px;
  color: #fff;
  font-size: 12px;
}
.client-info {
  margin: 0 auto;
  padding: 10px 0 0 0;
  font-size: 12px;
}
.client-info label {
  float: left;
  width: 40%;
  min-width: 76px;
  text-align: right;
  font-weight: normal;
  color: #666;
}
.client-info span {
  width: 60%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  float: left;
  color: #666;
}
.client-list {
  display: inline-block;
  position: relative;
  width: 100%;
  padding: 14px 20px 0 20px;
}
.client-list h4 {
  display: inline-block;
  height: 26px;
  width: 100%;
  border-bottom: solid 1px #f6f6f6;
  color: #5093e1;
  font-size: 14px;
  padding: 0 5px;
  margin-bottom: 10px;
}
.client-list .detail-more {
  position: absolute;
  width: 12%;
  float: right;
  font-size: 12px;
  color: #999999;
  cursor: pointer;
  right: 0;
  top: 14px;
}
.client-list i {
  padding-bottom: 4px;
}
.client-list label {
  float: left;
  width: 50%;
  min-width: 70px;
  text-align: right;
  font-weight: normal;
}
.client-list span {
  width: 50%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  float: left;
}
.client-list .cloud-ui-table {
  width: 95%;
  margin: 0 auto;
  border: none;
  font-size: 12px;
}
.client-list th {
  background-color: #fff;
  color: #999999;
  font-weight: 100;
}
.client-list .no-data {
  padding: 5px 0;
  text-align: center;
  font-size: 12px;
}
.client-content {
  margin: 0 auto;
  height: 25px;
  line-height: 25px;
  font-size: 12px;
  color: #666;
}
.client-search {
  width: 100px;
  height: 62px;
  margin: 0 auto;
  padding: 15px 0;
}



