.resetPassword .oper-top {
  margin: 0;
}
.resetPassword .oper-top .input-item,
.resetPassword .oper-top .search {
  padding-top: 6px;
  padding-bottom: 6px;
}
.resetPassword .oper-top .input-item:first-of-type {
  margin-right: 30px;
}
.resetPassword .first-line {
  margin-bottom: 0;
}
.resetPassword .first-line .input-item {
  height: 36px;
}
.resetPassword .first-line .input-normal {
  height: 35px;
  line-height: 35px;
  color: #999999;
}
.resetPassword .first-line .label-normal {
  float: left;
}
.resetPassword .input-item .label-normal {
  width: 94px;
}
.resetPassword .input-item {
  min-width: 280px;
}
.resetPassword .error {
  padding: 0;
  font-size: 14px;
}
.resetPassword .error-tips {
  padding-left: 126px;
}
.resetPassword .error-tips p {
  text-align: left;
}
.resetPassword .new-password {
  margin-right: 30px;
  float: left;
}
.resetPassword .new-password .error-tips {
  padding-left: 100px;
}
.resetPassword .form-title {
  padding-bottom: 13px;
}



