.operator-edit .form-group {
  padding-top: 15px;
}
.operator-edit .modal-footer {
  margin-top: 20px;
}
.operator-edit .error {
  padding: 0;
  font-size: 14px;
}
.operator-edit .error-tips {
  padding-left: 126px;
}
.operator-edit .error-tips p {
  text-align: left;
}
.operator-edit .textarea-normal {
  display: inline-block;
  width: 81%;
}
.operator-edit .textarea-normal .form-control {
  float: left;
  margin-left: 4px;
  height: 46px;
}
.operator-edit .col-sm-12 label {
  float: left;
}



