@import "~variable";
@import "~mixins";

.fansSpeechMonitor {
  .form-group .dropdown-menu li {
    width: 188px;
  }
  .form-group {
    height: 35px;
  }
  .form-group .input-item {
    min-width: 0;
  }
  .form-group .pull-right {
    margin-left: 10px;
  }
  .cloud-ui-table .box {
    width: 50px;
  }
  .cloud-ui-table .remark {
    display: inline-block;
    max-width: 200px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    .content {
      display: inline-block;
    }
  }
  .cloud-ui-table td {
    vertical-align: middle;
  }
  .cloud-ui-table .icon {
    margin-right: 10px;
    font-size: 14px;
    color: #f65177;
  }

  //用户信息提示窗样式
  .popover {
    max-width: none;
  }
  .popover-text {
    margin-bottom: 5px;
    font-weight: 300;
    .unit {
      display: inline-block;
      width: 100px;
      text-align: right;
      vertical-align: middle;
    }
    .content {
      display: inline-block;
      width: auto;
      vertical-align: middle;
    }
    .self-stock {
      background-color: orange;
      border-radius: 4px;
      color: #fff;
    }
    .error-text {
      background-color: #ce8483;
    }
  }
  .no-data {
    padding: 5px;
    text-align: center;
    font-size: 16px;
    color: #999;
    i {
      font-size: 80px;
      color: #999;
    }
  }
}



