@import "~variable";
@import "~mixins";

.viewpointDisplayRule {
  .form-base,
  .form-title:first-child,
  .form-group:not(:first-child) {
    overflow: hidden;
  }
  .form-group .form-radio {
    vertical-align: middle;
  }
  .form-group .radio-title {
    margin-right: 50px;
  }
  .form-title .form-header em {
    color: @btnRed;
    font-weight: normal;
  }
  .form-group .input-item .label-normal {
    width: 100px;
  }
  .form-group .input-item .input-normal {
    position: relative;
    vertical-align: middle;
  }
  .form-group .input-item .input-normal > span{
    position: absolute;
    top: 7px;
    right: 5px;
    color: @fontBase;
    i.icon-error {
      color: #999;
    }
  }
  .form-group .input-item .label-normal.label-special {
    width: 190px;
  }
  .cloud-ui-table .phone {
    width: 150px;
  }
  .cloud-ui-table tr td {
    max-width: 150px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  /*定制需求*/
  .selected-picture {
    display: inline-block;
    height: 96px;
  }
  .choose-picture {
    width: 100px;
    line-height: 35px;
    font-weight: normal;
    text-align: right;
    vertical-align: top;
    color: @fontBase;
  }
  .selected-picture i {
    position: absolute;
    top: 22px;
    left: 56px;
    padding-left: 14px;
    padding-top: 8px;
    width: 58px;
    height: 58px;
    background: #fff;
    border-radius: 50%;
    font-size: 28px;
    &:before {
      font-size: 40px;
      top: -6px;
      left: -6px;
      position: relative;
    }
  }
  .choose-background {
    position: relative;
    display: inline-block;
    margin-right: 15px;
    width: 170px;
    height: 100px;
    border-radius: 8px;
    background-color: #d8d8d8;
    color: #d8d8d8;
  }
  .choose-background img {
    position: absolute;
    top:0;
    width: 170px;
    height: 100px;
    border-radius: 8px;
  }
  .choose-file {
    width: 100%;
    height: 100%;
    opacity: 0;
  }
}



