@import "~variable";
@import "~mixins";

.recommendInvest {
  .form-base,
  .form-title:first-child,
  .form-group:not(:first-child) {
    overflow: hidden;
  }
  .form-group .form-radio {
    vertical-align: middle;
  }
  .form-group .radio-title {
    margin-right: 50px;
  }
  .form-title .form-header em {
    color: @btnRed;
    font-weight: normal;
  }
  .form-group .input-item {
    min-width: 0;
  }
  .form-group .input-item .input-normal {
    position: relative;
    vertical-align: middle;
    width: 170px;
  }
  .form-group .input-item .input-normal > span{
    position: absolute;
    top: 7px;
    right: 5px;
    color: @fontBase;
  }
  .form-group .input-item .label-normal {
    width: auto;
  }
  .form-group .input-item .label-normal.label-special {
    width: 190px;
  }
  .cloud-ui-table tr td {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
}



