@import "~variable";
@import "~mixins";

.risk-level-setting {
  .form-group {
    margin-bottom: 0;
    overflow: hidden;
  }
  .form-group .tip {
    margin-top: 5px;
    margin-right: 10px;
    float: right;
    color: #151E25;
  }
  .form-group .add {
    float: right;
  }
  .form-group em {
    color: #F65177;
    font-weight: normal;
  }
  .modal-content .form-group .input-item label {
    width: auto;
  }
}



