.operator-accredit {
  padding: 0;
  height: 460px;
}
.operator-accredit .form-group {
  padding-top: 15px;
}
.operator-accredit .form-group .tree-view {
  max-height: 250px;
}
.operator-accredit .modal-footer {
  margin-top: 20px;
}
.operator-accredit .template-modal {
  padding: 0 10px;
}
.operator-accredit .box-left,
.operator-accredit .box-right {
  width: 287px;
  height: 284px;
  margin: 3px;
  padding: 0;
  border: solid 1px #d7dfe1;
}
.operator-accredit .list-gray {
  height: 32px;
  background-color: #E0E2E8;
}
.operator-accredit .list-gray span {
  padding-left: 10px;
  height: 32px;
  line-height: 32px;
  color: #141e25;
}
.operator-accredit .form-title {
  padding-bottom: 0;
}
.operator-accredit .overbrim {
  width: 102px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  float: right;
}
.operator-accredit .col-sm-4 {
  padding-right: 0;
}
.operator-accredit .col-sm-4 label {
  width: 74px;
  text-align: right;
}



