.order-check .date-box {
  padding-top: 6px;
}
.order-check .date-box label {
  padding: 0;
  margin: 0;
  width: 120px;
  height: 35px;
  line-height: 35px;
  text-align: right;
}
.order-check .date-box .date-input-normal {
  margin-left: 3px;
  margin-right: 5px;
}
.order-check .form-title {
  overflow: hidden;
  padding-top: 10px;
  padding-bottom: 0;
}
.order-check .form-title .btn-normal {
  margin-left: 10px;
  float: right;
}
.order-check .cloud-ui-table {
  min-width: 2000px;
  overflow-x: auto;
}
.order-check .cloud-ui-table .operate {
  width: 200px;
}



