@import "~variable";
@import "~mixins";

.operator-manage {
  .first-line {
    .form-group {
      display: inline-block;
      width: 100%;
      margin-bottom: 0;
    }
    .content-top {
      padding-top: 6px;
      padding-bottom: 6px;
    }
  }
  .form-group .input-item {
    min-width: 280px;
      .label-normal {
      width: 84px;
    }
  }
  .search {
    padding-top: 6px;
  }
  .action-item {
    padding: 6px 0 6px 38px;
    text-align: left;
  }
  .icon {
    margin-right: 12px;
  }
  a {
    outline: none;
  }
  .list-width {
    padding: 8px 0;
  }
}
.search,
.add-more {
  display: inline-block;
  float: right;
}
.module-body .cloud-ui-table thead .operatorMore {
  padding-left: 94px;
  text-align: left;
}



