 @import "~variable";
 @import "~mixins";
 
 .productPoolManage {
  .form-group {
    margin-bottom: 0;
  }
  .form-header {
    display: inline-block;
  }
  .tips {
    display: inline-block;
    margin-left: 10px;
    font-size: 12px;
  }
  .tips em {
    color: @btnRed;
    font-weight: normal;
  }
  .search-item {
    float: right;
    margin-right: 20px;
  }
  .cloud-ui-table .red {
    color: @btnRed;
  }
  .cloud-ui-table .green {
    color: @btnGreen;
  }
  .cloud-ui-pagination {
    text-align: right;
  }
 }


