.order-modal {
  padding: 15px;
  width: 1000px;
  max-height: 460px;
  min-height: 150px;
  overflow: auto;
}
.order-modal .form-group {
  padding-top: 15px;
}
.order-modal .modal-footer {
  margin-top: 20px;
}
.order-modal .sub-list {
  max-height: 200px;
  overflow: auto;
  text-align: center;
}
.order-modal th {
  text-align: center;
}
.order-modal .form-title {
  padding-bottom: 0;
}



