@import "~variable";
@import "~mixins";

.viewpoint-package-check {
  .form-group {
    margin-bottom: 0;
    .select-list {
      padding-right: 15px;
      width: 150px;
      overflow: hidden;
      display: inline-block;
      text-overflow: ellipsis;
    }
    .select-dropdown {
      max-height: 300px;
      overflow: auto;
    }
  }
  .form-title {
    overflow: hidden;
    .search-item {
      float: right;
    }
  }
  .cloud-ui-table th {
    overflow: visible;
    .selected-block button {
      padding: 0;
      text-align: left;
      font-size: 14px;
      color: #333333;
      background-color: transparent;
      border: none;
      box-shadow: none;
    }
    .selected-block button i {
      font-size: 18px;
      margin-left: -3px;
      vertical-align: -2px;
    }
    .selected-block .select-dropdown {
      padding: 0;
      min-width: 110px;
      border-radius: 0;
      &:after {
        display: block;
        position: absolute;
        right: 48px;
        top: -10px;
        content: "";
        width: 0;
        height: 0;
        border-width: 5px;
        border-style: solid;
        border-color: transparent transparent white transparent;
      }
    }
    .selected-block .select-dropdown li {
      height: 35px;
      text-align: center;
      &:not(:last-of-type) {
        border-bottom: 1px solid #ddd;
      }
    }
    .selected-block .select-dropdown li a {
      line-height: 28px;
      &:hover {
        background-color: #5093e1;
        color: #fff;
      }
    }
  }
  .cloud-ui-table {
    .colorRed {
      color: #F55077;
    }
    .colorGreen {
      color: #50C14E;
    }
  }
}



