.invest-manage .centre-content .col-sm-8 {
  padding: 0;
}
.invest-manage .search-item {
  float: right;
  margin-right: 20px;
}
.invest-manage .table-content {
  height: 460px;
  margin-top: 0;
}
.invest-manage .table-content .cloud-ui-pagination {
  position: absolute;
  bottom: 0;
  right: 4px;
}
.invest-manage .table-content .detail-content {
  padding-top: 10px;
  padding-bottom: 20px;
}
.invest-manage .table-content label {
  font-weight: normal;
}
.invest-manage .table-content .table-detail .client-intro .client-symbol.limit {
  background-color: #f65177;
}
.invest-manage .table-content .input-item.phone {
  padding-left: 22px;
}
.invest-manage .content-list .col-sm-4 {
  padding: 0 15px;
}
.invest-manage .cloud-ui-pagination .pagination {
  margin: 5px;
}
.invest-manage .btn-blue {
  margin-right: 15px;
}
.invest-manage .cut-off {
  margin: 0 20px 5px 20px;
  border: dashed 1px #f6f6f6;
}
.invest-manage .client-search {
  width: 300px;
  text-align: center;
}
.invest-manage .client-search .btn-center {
  margin-left: 50px;
}
.invest-manage .client-search .btn-normal {
  float: none;
  margin-right: 10px;
}
.invest-manage .client-search .show-more {
  padding: 0;
}
.invest-manage .btn-normal {
  float: right;
}
.invest-manage .form-title {
  padding-bottom: 28px;
  margin: 0;
}
.invest-manage .client-info label,
.invest-manage .client-info span {
  color: #666;
}
.invest-manage .btn-green {
  padding: 0;
}
.invest-manage .btn-green a {
  display: inline-block;
  width: 100%;
  height: 100%;
  color: #fff;
}
.invest-manage .no-data {
  margin: 5px 0;
  text-align: center;
}



