@import "~variable";
@import "~mixins";

.store-selected-modal {
  .form-base,
  .form-group {
    margin-bottom: 30px;
  }
  .form-group .input-item label {
    width: auto;
    text-align: left;
  }
  .cloud-ui-table {
    margin: 0;
    max-width: 1000px;
  }
  .cloud-ui-table thead tr th {
    text-align: center;
  }
  .cloud-ui-table tbody tr td {
    text-align: center;
    border-right: 1px solid #ddd;
  }
  .cloud-ui-pagination {
    margin-top: 0;
    .pagination {
      margin-bottom: 0;
    }
  }
}



