@import "../variable";
@import "../mixins";

// loading
.modal-tips-preloader {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 13500;
  top: 0;
  left: 0;
  background-color: rgba(255,255,255,.9);
}

.spinner {
  width: 70px;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  min-height: 44px;
  transform: translate(-50%, -50%);
}

.spinner > div {
  width: 18px;
  height: 18px;
  background-color: @bgToaster;
  border-radius: 100%;
  display: inline-block;
  -webkit-animation: sk-bouncedelay 1.4s infinite ease-in-out both;
  animation: sk-bouncedelay 1.4s infinite ease-in-out both;
}

.spinner .bounce1 {
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}

.spinner .bounce2 {
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}

@-webkit-keyframes sk-bouncedelay {
  0%, 80%, 100% { -webkit-transform: scale(0) }
  40% { -webkit-transform: scale(1.0) }
}

@keyframes sk-bouncedelay {
  0%, 80%, 100% {
    -webkit-transform: scale(0);
    transform: scale(0);
  } 40% {
      -webkit-transform: scale(1.0);
      transform: scale(1.0);
    }
}

// toast
.toast-overlay {
  position: fixed;
  z-index: 13000;
  top: 0;
  left: 0;
  visibility: visible;
  width: 100%;
  height: 100%;
  -webkit-transition-duration: 400ms;
  transition-duration: 400ms;
  background-color: rgba(0,0,0,.5);
}

.toast-tips {
  width: 100%;
  position: fixed;
  z-index: 13500;
  top: 50%;
  left: 50%;
  min-height: 44px;
  transform: translate(-50%, -50%);
  text-align: center;
  color: @fontWhite;
  span {
    padding: 8px 15px;
    border-radius: 100px;
    background: rgba(0, 0, 0, .6);
  }
}



