.add-product-modal .form-group .input-item label {
  width: auto;
  text-align: left;
}
.add-product-modal .cloud-ui-table {
  margin: 0;
  max-width: 1000px;
  border: none;
}
.add-product-modal .cloud-ui-table thead tr th {
  text-align: center;
  color: #A6A6A6;
  border-bottom: none !important;
  background-color: transparent;
}
.add-product-modal .cloud-ui-table tbody tr td {
  text-align: center;
}
.add-product-modal .cloud-ui-table .red {
  color: #f65177;
}
.add-product-modal .cloud-ui-table .green {
  color: #50c14e;
}
.add-product-modal .cloud-ui-pagination {
  margin-top: 0;
}
.add-product-modal .cloud-ui-pagination .pagination {
  margin-bottom: 0;
}



