@import "~variable";
@import "~mixins";

.invest-manage {
  .centre-content {
    .col-sm-8 {
      padding: 0;
    }
  }
  .search-item {
    float: right;
    margin-right: 20px;
  }
  .table-content {
    height: 460px;
    margin-top: 0;
    .cloud-ui-pagination {
      position: absolute;
      bottom: 0;
      right: 4px;
    }
    .detail-content {
      padding-top: 10px;
      padding-bottom: 20px;
    }
    label {
      font-weight: normal;
    }
    .table-detail .client-intro .client-symbol.limit {
      background-color: @btnRed;
    }
    .input-item.weixinNo {
      //padding-left: 55px;
    }
    .input-item.phone {
      padding-left: 22px;
    }
  }
  .content-list {
    .col-sm-4 {
      padding: 0 15px;
    }
  }

  .cloud-ui-pagination{
    .pagination {
      margin: 5px;
    }
  }
  .btn-blue {
    margin-right: 15px;
  }
  .cut-off {
    margin: 0 20px 5px 20px;
    border: dashed 1px @clientBorder;
  }
  .client-search {
    width: 300px;
    text-align: center;
    .btn-center {
      margin-left: 50px;
    }
    .btn-normal {
      float: none;
      margin-right: 10px;
    }
    .show-more {
      padding: 0;
    }
  }
  .btn-normal {
    float: right;
  }
  .form-title {
    padding-bottom: 28px;
    margin: 0;
  }
  .client-info {
    label,
    span {
      color: @fontBase;
    }
  }
  .btn-green {
    padding: 0;
    a {
      display: inline-block;
      width: 100%;
      height: 100%;
      color: @fontWhite;
    }
  }
  .no-data {
    margin: 5px 0;
    text-align: center;
  }
}



